<template>
  <div class="form_wrapper" @click.stop="show_modal">
            <div class="form_title">
              
            </div>
            <div class="form_content">
                <ul>
                    <li>
                        <h4>FAQ</h4>
                        <p>{{form_content.faq_id}}</p>
                    </li>
                    <li>
                        <h4></h4>
                        <template v-if="form_content.type==1">
													<p></p>
												</template>
												<template v-if="form_content.type==2">
													<p></p>
												</template>
												<template v-if="form_content.type==3">
													<p></p>
												</template>
                    </li>
                    <li>
                        <h4></h4>
                        <template v-if="form_content.source==1">
													<p></p>
												</template>
                    </li>
                    <li>
                        <h4></h4>
                        <p>
													<span v-for="(n,i) in form_content.similar" :key="i">{{n}}</span>
												</p>
                    </li>
                </ul>
            </div>
          </div>
</template>
<script>
export default {
  name: "history_form",
  data() {
    return {
      form_content: [
      ]
    };
  },
  props: ["getData"],
  created() {},
  mounted() {
		// alert(5)
		console.log(this.getData,4545);
		var temp_id = this.getData.id-0;
    var params = {
      'id': temp_id
    };
		console.log(params,7788);
    this.$cm.ajax(
      "POST",
      "Faq.faq_manageinfo",
      params,
      res => {
        console.log(res);
				this.form_content = res.data.result.data[0];
      },
      this
    );
  },
  methods: {
    show_modal() {
      this.$parent.show_fade = true;
    }
  }
};
</script>
<style lang="less" scoped>
.form_wrapper {
  text-align: left;
  .form_title {
    line-height: 40px;
    color: #333333;
    font-size: 14px;
    padding-left: 20px;
    height: 40px;
    border-bottom: 1px solid #f5f5f5;
  }
  .form_content {
    ul {
      padding-top: 20px;
      li {
        padding: 30px 0px 0px 20px;
        h4 {
          font-size: 14px;
          color: #b2b2b2;
        }
        &>p {
          font-size: 12px;
          columns: #333333;
          padding: 14px 0px 20px 0px;
					border-bottom: 1px solid #f5f5f5;
					span {
						display:block;
						padding:8px 0px;
					}
        }
      }
    }
  }
}
</style>




