<template>
  <div class="img_uploader">
    <h4></h4>
    <!-- <div class="imgSrc" v-show="!show_temp">  
      <img :src="newUrl" >
    </div>  -->
    <template v-for="(item,index) in newUrl">
      <div class="imgSrc" v-show="newUrl.length>0" :key="item">
        <img :src="item">
        <div class="del-button" @click.stop="del_url(index)"></div>
      </div> 
    </template>
    <label for="up_img" class="img_uploader_label">+</label>
    <br>
    <input type="file" id="up_img" name="file" class="upload__input" @change="uploadChange" accept="image/gif,image/jpeg,image/jpg,image/png,image/svg">  
    <!-- <div class="add" @click="addSub" ref="hide_up">quesadding</div> -->
  </div>
</template>
<script>
export default {
  name: "img_uploader",
  data() {
    return {
      vlaue: "",
      imgDataUrl: "",
      file: [],
      newUrl: [],
      show_temp: true
    };
  },
  mounted() {
    this.newUrl = this.imgurl;
  },
  props: ["imgurl"],
  mounted() {
    // console.log(this.imgurl );
  },
  watch: {
    imgurl(n, o) {
      this.newUrl = n;
    }
  },
  methods: {
    upload_img() {
      this.$refs.hide_up.onclick;
    },
    uploadChange(event) {
      if (event.target.files.length > 0) {
        this.files = event.target.files[0]; //
        console.log(this.files, 5688);
        this.$cm.getBase64(event.target, url => {
          this.imgDataUrl = "data:image/png;base64," + url; //
        });
        if(this.files.size){

        };
        if (this.newUrl.length >= 3) {
          this.$alert("");
        } else {
          this.addSub();
        }
        // this.$cm.ajax("POST","Faq.faq_upload",{},()=>{},this)
        //
      }
    },
    addSub() {
      let data = {};
      let files = this.files;
      let param = new FormData(); //form
      if (files != "") {
        param.append("file", files, files.name); //  append 
      } else {
        this.$message.error("");
      }
      param.append("param", JSON.stringify(data)); //form
      let config = {
        headers: { "Content-Type": "multipart/form-data" }
      };
      //
      this.$http({
        method: "POST",
        url: this.$cm.baseUrl + "Faq.faq_upload",
        body: param,
        config
      })
        .then(function(res) {
          console.log(this);
          this.newUrl.push(res.data.result.img);
          this.imgDataUrl = "";
          this.files = [];
          // this.show_temp = false;
          this.$emit("toImg", this.newUrl);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    del_url(index) {
      console.log(index);
      this.newUrl.splice(index, 1);
      this.newUrl = this.newUrl.length == 0 ? [] : this.newUrl;
      this.$emit("toImg", this.newUrl);
    }
  }
};
</script>
<style lang="less">
@import "../../assets/mixin/minxin.less";
.img_uploader {
  position: relative;
  h4 {
    padding-bottom: 10px;
  }
  width: 100%;
  // max-height:120px;
  overflow: auto;
  img {
    width: 100px;
    height: 100px;
  }
  .imgSrc {
    position: relative;
    .del-button {
      position: absolute;
      display: none;
      width: 30px;
      height: 30px;
      top: 0px;
      right: 0px;
      background: rgba(250, 250, 250, 0.5) url(../../assets/delete.png)
        no-repeat center center;
      background-size: 50% 50%;
      cursor: pointer;
    }
    img {
      &:active {
        border:1px solid #ccc;
        .middle();
        position: fixed;
        height:auto;
        width:auto;
        max-width:400px;
        max-height:400px;
        object-fit: convert;
      }
    }
    &:hover {
      .del-button {
        display: block;
      }
    }
  }
  .imgSrc,
  .img_uploader_label {
    display: inline-block;
    height: 100px;
    width: 100px;
    background-color: #f5f5f5;
    border-radius: 10px;
    vertical-align: middle;
    overflow: hidden;
  }
  .img_uploader_label {
    font-size: 60px;
    color: #999999;
    line-height: 100px;
    text-align: center;
  }
  #up_img {
    display: none;
  }
}
</style>





