<template>
  <div class="form_wrapper" @click.stop="keep_modal">
            <div class="form_title">
              
            </div>
            <div class="form_content">
							<div class="titleBox">
								<h4></h4>
								<p>{{alike.ques}}</p>
							</div>
								<!--  -->              
              <div class="recommend yo-el-checkbox inputBox">
                <h4>({{checkList_1.length}})</h4>
								<div class="inputValue yo-el-input-add">
                  <el-input :span='6' placeholder="" v-model="standard_answer" ></el-input>
									<el-button class="yo-el-input-add" @click="add_similar(standard_answer)">+ </el-button>
                </div>
                <el-checkbox-group v-model="checkList_1" @change="end_value_1">
                  <template v-for="(item,index) in already_similar_list">
                    <el-checkbox :label="item.question" :key="index" checked></el-checkbox>                      
                  </template>
                </el-checkbox-group>
                <!-- <ul>
                  <li v-for="(item,index) in recommend_list" :key="index">
                    <span>{{item.content}}</span><el-checkbox @click="setValue($event,index)"></el-checkbox>
                  </li>
                </ul> -->
              </div>
              
              <!--  -->
              <div class="recommend yo-el-checkbox">
                <h4>({{checkList_2.length}})</h4>
                <el-checkbox-group v-model="checkList_2" @change="end_value_2">
                  <template v-for="(item,index) in recommend_list">
                    <el-checkbox :label="item.question" :key="index"></el-checkbox>                      
                  </template>
                </el-checkbox-group>
                <!-- <ul>
                  <li v-for="(item,index) in recommend_list" :key="index">
                    <span>{{item.content}}</span><el-checkbox @click="setValue($event,index)"></el-checkbox>
                  </li>
                </ul> -->
              </div>
            </div>
            <div class="form_btn">
                <!-- <input type="button" class="yo-submit-btn" value="">
                <input type="button" class="yo-cancle-btn" value=""> -->
                <div class="yo-btn">
                    <el-button round @click.stop="close_modal"></el-button>
                  <el-button type="primary" round @click.stop="save_similar(alike.id,slectedAreadySimilar,slectedrecommendSimilar)"></el-button>
                </div>
            </div>
          </div>
</template>
<script>
import edit_form from "../edit_form.vue";
export default {
  name: "history_form",
  data() {
    return {
      standard_answer:'',
      checkList_1: [],
      checkList_2:[],
      recommend_list: [
      ],
      slectedAreadySimilar:'',
      slectedrecommendSimilar:'',
      already_similar_list:[],
      confirm_modal: false,
      search_show: true,
      add_show: false,
    };
  },
  props:{
    'alike':{
      type: Object
    }
  },
  watch:{
    alike:function(n,o){
     
    }
  },
  mounted () {
    // console.log(showModal);
    // console.log(this.alike);
      this.$cm.ajax("GET","Faq.faq_questionsimilar",{id:this.alike.id},(res)=>{
            // console.log(res) 
            this.already_similar_list = res.data.result.similar;
            this.recommend_list = res.data.result.similar_data;
            this.slectedAreadySimilar = res.data.result.similar;
      },this);     
  },
  components: {
    edit_form
  },
  methods: {
    //
    save_similar(id,similar,similar_data){
      this.$cm.ajax("GET","Faq.faq_savesimilar",{id:id,similar:similar,similar_data:similar_data},(res)=>{
            // console.log(res);
          this.$parent.show_fade = false;
          this.$cm.ajax("GET","Faq.faq_getquestion",'',(res)=>{
            console.log(res);
            this.$parent.faq_ques = res.data.result.data;
          },this); 
      },this);       
    },
    //;
    add_similar(ques){
        // console.log(ques);
        var flag = true;
        if(ques){
          if(this.already_similar_list.length != 0){
            for (var i = 0; i < this.already_similar_list.length; i++) {
              if(this.already_similar_list[i].question == ques){
                 flag = false;  
              }
            };
            if(flag){
              this.already_similar_list.push({id:'',question:ques});  
            }
          }else{
            this.already_similar_list.push({id:'',question:ques});
          }       
        }
        // console.log(this.already_similar_list);
    },
    close_modal() {
      this.$parent.show_fade = false;
    },
    keep_modal() {
      this.$parent.show_fade = true;
    },
    setValue() {},
    end_value_1() {      
      this.slectedAreadySimilar = [];    
      for (var i = 0; i < this.checkList_1.length; i++) {
        this.slectedAreadySimilar.push({
          id:'',
          question: this.checkList_1[i]
        })     
      };
      for (var i = 0; i < this.checkList_1.length ; i++) {
        for (var j = 0; j < this.already_similar_list.length; j++) {
          if( this.checkList_1[i] == this.already_similar_list[j].question){
            this.slectedAreadySimilar[i].id = this.already_similar_list[j].id;
          }
        };      
      };   
      if(this.slectedAreadySimilar.length == 0){
        this.slectedAreadySimilar ="";
      }   
      console.log(this.slectedAreadySimilar);      
    },
    end_value_2() {  
      this.slectedrecommendSimilar = [];
      for (var i = 0; i < this.checkList_2.length; i++) {
        this.slectedrecommendSimilar.push({
          question: this.checkList_2[i]
        })     
      };     
      for (var i = 0; i < this.checkList_2.length ; i++) {
        for (var j = 0; j < this.recommend_list.length; j++) {
          if( this.checkList_2[i] == this.recommend_list[j].question){
            this.slectedrecommendSimilar[i].id = this.recommend_list[j].id;
          }
        };      
      };
      // console.log(this.slectedrecommendSimilar);          
    },    
  }
};
</script>
<style lang="less" scoped>
.text_es() {
  overflow: hidden;
  text-overflow: ellipsis;
  /* */
  display: -webkit-box;
  /* */
  -webkit-line-clamp: 1;
  /*  */
  -webkit-box-orient: vertical;
}
.require {
  padding-left: 14px;
  position: relative;
  &::before {
    position: absolute;
    left: 0px;
    top: 7px;
    content: "";
    display: block;
    height: 4px;
    width: 4px;
    background-color: #ff0000;
  }
}
.addOther {
  display: inline-block;
  color: #409eff;
  padding: 12px 0px;
  cursor: pointer;
}
.form_wrapper {
  text-align: left;
  display: flex;
  flex-direction: column;
  /*  */
  .form_title {
    line-height: 40px;
    color: #333333;
    font-size: 14px;
    padding-left: 20px;
    height: 40px;
    border-bottom: 1px solid #f5f5f5;
  }
  /*  */
  .form_content {
    flex: 1;
    padding: 24px;
		overflow: auto;
		.titleBox {
      color:#b2b2b2;
			p {
        padding:16px 0px 0px 0px;
				font-size: 16px;
				color:#333333;
			}
		}
    .selectBox {
      margin-bottom: 20px;
      .el-select {
        padding: 10px 5px;
        border-radius: 53px;
        overflow: hidden;
        font-size: 14px;
        color: #333333;
      }
      .selectValue {
        display: flex;
        width: 100%;
        height: 100%;
      }
      .selectLabel {
        width: 100%;
      }
    }
    .inputValue {
      margin-top: -6px;
      margin-bottom: 20px;
			height: 36px;
      display: flex;
      .yo-el-input-add {
        width:100px;
        height:100%;
        border-radius: 17px;
        margin-left:14px;
        padding:0px;
        span {
          height:34px;
        }
      }
      input {
        height: 100%;
      }
    }
    .alike_box {
      margin-bottom: 20px;
      position: relative;
      h4 {
        color: #b2b2b2;
      }
      .alike_q {
        padding: 10px 0px 0px 0px;
        color: #409eff;
        max-width: 70%;
        .text_es();
        cursor: pointer;
      }
      .modal_q {
        height: 68px;
        width: 180px;
        border: 1px solid #dcdfe6;
        border-radius: 0px 12px 0px 0px;
        position: absolute;
        right: 0px;
        top: 0px;
        text-align: center;
        padding: 10px;
        background-color: #fefefe;
        box-shadow: 3px 4px #cccccc;
        h4 {
          padding-bottom: 8px;
        }
        button {
          height: 22px;
          width: 48px;
          font-size: 12px;
          display: inline-block;
          // line-height: 1;
          white-space: nowrap;
          cursor: pointer;
          background: #fff;
          border: 1px solid #dcdfe6;
          color: #606266;
          -webkit-appearance: none;
          text-align: center;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          outline: 0;
          margin: 0;
          -webkit-transition: 0.1s;
          transition: 0.1s;
          font-weight: 500;
          line-height: 22px;
          border-radius: 14px;
          margin: 0px 8px;
          &:hover {
            color: #409eff;
            border-color: #c6e2ff;
            background-color: #ecf5ff;
          }
          &:nth-last-child(1) {
            background-color: #409eff;
            color: #ffffff;
            &:hover {
              background: #66b1ff;
              border-color: #66b1ff;
              color: #fff;
            }
          }
        }
      }
    }
    .ql_content {
      margin-top: 10px;
      // margin-bottom:20px;
    }
    .recommend {
      h4 {
        padding-top: 48px;
        padding-bottom: 20px;
      }
      .el-checkbox {
        padding-bottom: 14px;
      }
    }
  }
  /*  */
  .form_btn {
    text-align: right;
    padding: 24px;
    .yo-btn {
      .el-button {
        padding: 11px 25px;
      }
      .el-button + .el-button {
        margin-left: 20px;
      }
    }
  }
}
</style>




