<template>
  <div id="login">
    <div class="login_form">
      <div class="login_top"></div>
        <div class="form_content">
          <el-form ref="form" :model="form" label-width="">
            <el-form-item>
              <div class="login-input">
                <el-input type="text" v-model="form.name" placeholder="" focus></el-input>
              </div>
            </el-form-item>
            <el-form-item>
              <div class="login-input">
                <el-input type="password" v-model="form.password" placeholder=""></el-input>
              </div>
            </el-form-item>
            <el-form-item>
              <div class="login-input">
                <div class="code_word">
                  <el-input type="text" v-model="form.code" placeholder=""></el-input>
                </div>
                <div class="code_image" @click="code_url=code_url+Math.random()">
                  <img :src="code_url" alt="" srcset="">
                </div>
              </div>
            </el-form-item>
            <el-form-item>
              <div class="login-input checked-box">
                <el-checkbox v-model="checked" ></el-checkbox>
              </div>
            </el-form-item>
            <el-form-item>
              <div class="login-input submit-box">
                <el-button type="primary" @click="onSubmit"></el-button>
              </div>
            </el-form-item>
          </el-form>
        </div>
      </div>
  </div>
</template>
<script>
export default {
  name: "",
  data() {
    return {
      img_id: "",
      code_url: "",
      form: {
        name: "",
        password: "",
        code: ""
      },
      beShow: false, //
      checked: false
    };
  },
  created() {
    // console.log(this.$cm.getCookie());
    var cookie = this.$cm.getCookie();
    // console.log(cookie,"jiazahuoqu");
    this.$cm.ajax(
      "POST",
      "Faq.faq_getsessionid",
      {},
      res => {
        this.img_id = res.data.result.sessionid;
        // console.log(this.img_id);
        this.code_url = this.$cm.baseUrl + "Faq.faq_vcode&sid=" + this.img_id;
      },
      this
    );
    if (!cookie.uid) {
      this.$parent.login_show = true;
    } else {
      this.$parent.login_show = false;
      this.$cm.uid = cookie.uid;
    }
  },
  props: ["fromParent"],
  mounted() {
    // var temp = this.$cm.getCookie();
    // if(temp.userName&&temp.userPwd){
    //   form.name = temp.userName;
    //   form.password = temp.userPwd;
    // }
  },
  methods: {
    //
    onSubmit() {
      this.$http({
        method: "POST",
        url: this.$cm.baseUrl + "Faq.faq_login",
        params: {
          account: this.form.name,
          password: this.form.password,
          vcode: this.form.code,
          sid: this.img_id
        },
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
        // aiic99e4sv2ehmrds2tagq9m36
        // body: {
        //   account: this.form.name,
        //   password: this.form.password,
        //   vcode: this.form.code
        // },
        // emulateJSON: true
      })
        .then(function(res) {
          console.log(res);
          console.log(this.$cm.setCookieUid(res.data.result.id, 7));
          this.$cm.uid = this.$cm.getCookie().uid;
          if (res.data.code==200) {
            this.$parent.login_show = false;
            this.$router.push({ path: "/" });
          }else {
            this.$alert(res.data.message);
          }
        })
        .catch(function(error) {
          console.log(error);
        });

      if (this.checked) {
        this.$cm.setCookie(this.form.name, this.form.password, 7);
        // console.log(this.$cm.getCookie(), "!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!");
      }
    },
    resetForm: function() {
      this.formName.user = "";
      this.formName.userError = "";
      this.formName.password = "";
      this.formName.passwordError = "";
    },
    // submitForm: function(formName) {
    //   //
    //   //this.$emit('showState', [this.beShow,this.formName.user])
    //   //user password
    //   var user = this.formName.user,
    //     password = this.formName.password;
    //   console.log(user, password);
    //   Axios.get(
    //     "../../src/php/login.php?user=" + user + "&password=" + password
    //   )
    //     .then(function(res) {
    //       console.log(res);
    //     })
    //     .catch(function() {});
    // },
    inputBlur: function(errorItem, inputContent) {
      if (errorItem === "user") {
        if (inputContent === "") {
          this.formName.userError = "";
        } else {
          this.formName.userError = "";
        }
      } else if (errorItem === "password") {
        if (inputContent === "") {
          this.formName.passwordError = "";
        } else {
          this.formName.passwordError = "";
        }
      }
      //
      if (this.formName.user != "" && this.formName.password != "") {
        this.formName.beDisabled = false;
      } else {
        this.formName.beDisabled = true;
      }
    }
  }
};
</script>

<style lang="less" scoped>
#login {
  height: 100%;
  width: 100%;
  position: relative;
  background-color: #f3f3f3;
}
.middle() {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
@line: 1px solid #edf0f5;
@color: #4292ee;
.login_form {
  height: 490px;
  width: 350px;
  .middle();
  border: @line;
  border-radius: 10px;
  overflow: hidden;
  background-color: #ffffff;
  .login_top {
    height: 114px;
    width: 100%;
    background: url(../../assets/login_top.png) no-repeat center center;
    background-size: 100%;
  }
  .form_content {
    padding: 40px 36px;
    text-align: center;
    .login-input {
      width: 100%;
      text-align: left;
    }
    .login-input.submit-box {
      width: 100%;
      text-align: center;
    }
    .login-input.check-box {
      width: 100%;
      text-align: left;
    }
    .code_word {
      display: inline-block;
      height: 40px;
      width: 166px;
      text-align: left;
    }
    .code_image {
      display: inline-block;
      height: 40px;
      width: 96px;
      vertical-align: middle;
      border: 1px solid #f7f7f7;
      background-color: #f7f7f7;
      border-radius: 20px;
      float: right;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  .login-input {
    height: 40px;
    input {
      margin-left: 0px;
    }
  }
}
</style>




