<template>
  <div class="form_wrapper" @click.stop="keep_modal">
            <div class="form_title">
              FAQ
            </div>
            <div class="form_content">
              <!-- select -->
              <div class="selectBox">              
                <div class="selectLabel">
                  <p class="require"></p>
                </div>
                <div class="selectValue">
                  <template v-for="(item,key,index) in getchooseclass">
                    <el-select v-model="value[index]" placeholder="" :key="index" @change="changeData(value,index)">
                      <el-option
                        v-for="item_1 in item"
                        :key="item_1.value"
                        :label="item_1.label"
                        :value="item_1.value">
                      </el-option>
                    </el-select>
                  </template>
                </div>
              </div>
              <!--  -->
              <div class="inputBox">
                <div class="inputTitle">
                  <p class="require"></p>
                </div>
                <div class="inputValue yo-el-input">
                  <el-input :span='6' placeholder="" v-model="similarAsk"  v-on:input="getsimilarfaq()"></el-input>
                </div>
                <div class="alike_box" v-show="search_show">
                  <h4>FAQ</h4>
                  <p v-for="n in similarFaq" class="alike_q" @click="confirm_modal=true" :key="n.id"><span>{{n.question}}</span></p>
                  <div class="modal_q" v-show="confirm_modal">
                    <h4>FAQ</h4>
                    <p @click="confirm_modal=false">
                      <button></button>                
                      <button></button>
                    </p>
                  </div>
                </div>                
              </div>
              <!--  -->
              <div class="edit">
                <div class="editTitle">
                  <p class="require"></p>
                  <edit_form class="ql_content" v-on:answer="answer"></edit_form>
                </div>
              </div>
              <div>
                <img_uploader v-on:toImg="showImgFromChild"></img_uploader>  
              </div>
              <!--  -->
              <div class="recommend yo-el-checkbox" v-show="search_show">
                <h4>({{checkList.length}})</h4>
                <el-checkbox-group v-model="checkList" >
                  <template v-for="(item,index) in getsimilarask">
                    <el-checkbox :label="item.question" :key="index" :id="item.id" @change="end_value(item.id)"></el-checkbox>
                  </template> 
                </el-checkbox-group>
                <!-- <ul>
                  <li v-for="(item,index) in recommend_list" :key="index">
                    <span>{{item.content}}</span><el-checkbox @click="setValue($event,index)"></el-checkbox>
                  </li>
                </ul> -->
              </div>
            </div>
            <div class="form_btn">
                <div class="yo-btn">
                    <el-button round @click.stop="close_modal"></el-button>
                    <el-button type="primary" round @click.stop="saveNewFaq(similarAsk,faqAnswer,imgurl,value,slectedSimilarAsk)"></el-button>
                </div>
            </div>
          </div>
</template>
<script>
import edit_form from "../edit_form.vue";
import img_uploader from "../img_uploader.vue";
export default {
  name: "history_form",
  data() {
    return {
      idArray: [],
      value: [],
      similarAsk: "",
      slectedSimilarAsk: [],
      faqAnswer: "",
      imgurl: [],
      similarFaq: [],
      getsimilarask: [],
      checkList: [],
      recommend_list: [],
      confirm_modal: false,
      search_show: false,
      add_show: false,
      value0: "",
      value1: "",
      value2: "",
      getchooseclass: {},
      form_content: [
        {
          title: "FAQ",
          content: "[]",
          word: ""
        },
        {
          title: "",
          content: "",
          word: ""
        },
        {
          title: "",
          content: "",
          word: ""
        },
        {
          title: "",
          content: "",
          word: ""
        }
      ]
    };
  },
  props: ["getmbxContentId"],
  mounted() {
    //
    this.init_id();
  },
  components: {
    edit_form,
    img_uploader
  },
  methods: {
    init_emity_select() {
      //
      this.$cm.ajax(
        "GET",
        "Faq.faq_getchooseclass",
        {},
        res => {
          this.getchooseclass = res.data.result.data;
        },
        this
      );
      console.log(this.getchooseclass);
    },
    /* idselect */
    init_id() {
      console.log(this.getmbxContentId, "");
      if (this.getmbxContentId.length == 0) {
        this.init_emity_select();
      } else {
        this.value = [];
        var idArray = this.getmbxContentId.split(".");
        console.log(idArray);
        var firstParam = idArray
          .slice(idArray.length - 2, idArray.length)
          .join(".");
        console.log(firstParam);
        this.$cm.ajax(
          "POST",
          "Faq.faq_getchooseclass",
          { id: firstParam },
          res => {
            this.getchooseclass = res.data.result.data;
            this.value = res.data.result.choose;
          },
          this
        );
      }
    },
    /* select */
    changeData(val, i) {
      var param = val[i];
     if (i > 0 && val[i] == "") {
        param = val[i - 1];
      } else {
        var temp = this.value;
        for (var j = 1; j < temp.length - i; j++) {
          this.value[i + j] = "";
        }
        console.log(this.value);
      }
      console.log(param);
      // this.form_push.class = param;
      this.$cm.ajax(
        "POST",
        "Faq.faq_getchooseclass",
        { id: param },
        res => {
          this.getchooseclass = res.data.result.data;
          this.value = res.data.result.choose;
          console.log(this.value);
        },
        this
      );
    },
    //imgURL
    showImgFromChild(data) {
      this.imgurl = data;
    },
    answer(data) {
      console.log(data);
      this.faqAnswer = data;
    },
    //faq
    saveNewFaq(question, answer, img, getClass, similar_data) {
      var alike = similar_data;
      alike.length === 0 ? (alike = [""]) : alike;
      this.$cm.ajax(
        "POST",
        "Faq.faq_savenewfaq",
        {
          question: question,
          answer: answer,
          img: img,
          class: getClass,
          similar_data: alike
        },
        res => {
          console.log(res);
          this.close_modal();
          this.$parent.init_home_data();
        },
        this
      );
    },
    close_modal() {
      this.value = [];
      this.$parent.show_fade = false;
    },
    keep_modal() {
      this.$parent.show_fade = true;
    },
    setValue() {},
    end_value(id) {
      console.log(this.getsimilarask);
      this.slectedSimilarAsk = [];
      for (var i = 0; i < this.checkList.length; i++) {
        this.slectedSimilarAsk.push({
          question: this.checkList[i]
        });
      }
      for (var i = 0; i < this.checkList.length; i++) {
        for (var j = 0; j < this.getsimilarask.length; j++) {
          if (this.checkList[i] == this.getsimilarask[j].question) {
            this.slectedSimilarAsk[i].id = this.getsimilarask[j].id;
          }
        }
      }
      // console.log(this.slectedSimilarAsk);
    },
    //faq
    getsimilarfaq() {
      this.search_show = true;
      this.$cm.ajax(
        "POST",
        "Faq.faq_getsimilarfaq",
        { ask: this.similarAsk },
        res => {
          this.similarFaq = res.data.result.data;
        },
        this
      );
      this.$cm.ajax(
        "POST",
        "Faq.faq_getsimilarask",
        { ask: this.similarAsk },
        res => {
          this.getsimilarask = res.data.result.data;
        },
        this
      );
    }
  }
};
</script>
<style lang="less" scoped>
.text_es() {
  overflow: hidden;
  text-overflow: ellipsis;
  /* */
  display: -webkit-box;
  /* */
  -webkit-line-clamp: 1;
  /*  */
  -webkit-box-orient: vertical;
}
.require {
  padding-left: 14px;
  position: relative;
  &::before {
    position: absolute;
    left: 0px;
    top: 7px;
    content: "";
    display: block;
    height: 4px;
    width: 4px;
    background-color: #ff0000;
  }
}
.addOther {
  display: inline-block;
  color: #409eff;
  padding: 12px 0px;
  cursor: pointer;
}
.form_wrapper {
  text-align: left;
  display: flex;
  flex-direction: column;
  /*  */
  .form_title {
    line-height: 40px;
    color: #333333;
    font-size: 14px;
    padding-left: 20px;
    height: 40px;
    border-bottom: 1px solid #f5f5f5;
  }
  /*  */
  .form_content {
    flex: 1;
    padding: 24px;
    overflow: auto;
    .selectBox {
      margin-bottom: 20px;
      .el-select {
        padding: 10px 5px;
        border-radius: 53px;
        overflow: hidden;
        font-size: 14px;
        color: #333333;
      }
      .selectValue {
        display: flex;
        width: 100%;
        height: 100%;
      }
      .selectLabel {
        width: 100%;
      }
    }
    .inputValue {
      margin-top: 10px;
      margin-bottom: 20px;
      height: 36px;
      input {
        height: 100%;
      }
    }
    .alike_box {
      margin-bottom: 20px;
      position: relative;
      h4 {
        color: #b2b2b2;
      }
      .alike_q {
        padding: 10px 0px 0px 0px;
        color: #409eff;
        max-width: 70%;
        .text_es();
        cursor: pointer;
      }
      .modal_q {
        height: 68px;
        width: 180px;
        border: 1px solid #dcdfe6;
        border-radius: 0px 12px 0px 0px;
        position: absolute;
        right: 0px;
        top: 0px;
        text-align: center;
        padding: 10px;
        background-color: #fefefe;
        box-shadow: 3px 4px #cccccc;
        h4 {
          padding-bottom: 8px;
        }
        button {
          height: 22px;
          width: 48px;
          font-size: 12px;
          display: inline-block;
          // line-height: 1;
          white-space: nowrap;
          cursor: pointer;
          background: #fff;
          border: 1px solid #dcdfe6;
          color: #606266;
          -webkit-appearance: none;
          text-align: center;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          outline: 0;
          margin: 0;
          -webkit-transition: 0.1s;
          transition: 0.1s;
          font-weight: 500;
          line-height: 22px;
          border-radius: 14px;
          margin: 0px 8px;
          &:hover {
            color: #409eff;
            border-color: #c6e2ff;
            background-color: #ecf5ff;
          }
          &:nth-last-child(1) {
            background-color: #409eff;
            color: #ffffff;
            &:hover {
              background: #66b1ff;
              border-color: #66b1ff;
              color: #fff;
            }
          }
        }
      }
    }
    .ql_content {
      margin-top: 10px;
      // margin-bottom:20px;
    }
    .recommend {
      h4 {
        padding-top: 26px;
        padding-bottom: 20px;
      }
      .el-checkbox {
        padding-bottom: 14px;
      }
    }
  }
  /*  */
  .form_btn {
    text-align: right;
    padding: 24px;
    .yo-btn {
      .el-button {
        padding: 11px 25px;
      }
      .el-button + .el-button {
        margin-left: 20px;
      }
    }
  }
}
</style>




