import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

//
import home from '@/component/pages/home.vue';
import manager from '@/component/pages/manager.vue';
import find from '@/component/pages/find.vue';
import history from '@/component/pages/history.vue';
import test from '@/component/pages/test.vue';
import info from '@/component/pages/info.vue';
import words_manager from '@/component/pages/words_manager.vue';
import analyse from '@/component/pages/analyse.vue';
//

//

const router = new VueRouter({
  mode: 'hash',
  base: __dirname,
  routes: [{
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/manager',
      name: 'manager',
      component: manager
    }, {
      path: '/find',
      name: 'find',
      component: find
    }, {
      path: '/history',
      name: 'history',
      component: history
    }, {
      path: '/test',
      name: 'test',
      component: test
    }, {
      path: '/info',
      name: 'info',
      component: info
    }, {
      path: '/words_manager',
      name: 'words_manager',
      component: words_manager
    },
    {
      path: '/analyse',
      name: 'analyse',
      component: analyse
    }

  ]
});
export default router;


