<template>
  <div class="find">
    <div class="dateChange">
      <el-date-picker v-model="ChangeTime" size="mini" type="daterange" align="right" unlink-panels range-separator="-" start-placeholder="" end-placeholder="" :picker-options="pickerOptions2" format="yyyy / MM / dd " value-format="yyyy-MM-dd">
      </el-date-picker>
    </div>
    <ul class="ul_wrapper">
      <template v-for="(n,i) in car_data">
        <li :key="i" class="list_group">
          <div class="list_wrapper">
            <div class="list_top">
              <h3 :title="n.title.question">{{n.title.question}}</h3>
              <span class="temp_icon">
                <i class="el-icon-delete" title="" @click="dele_card(car_data_delet[i].id)"></i>
                <i class="el-icon-news" title="FAQ" @click="merge_card(n.title.question,n.similar,n.title.id)"></i>
                <i class="el-icon-circle-plus-outline" title="FAQ" @click.stop="add_card(n.title.question,n.similar,n.title.id)"></i>
              </span>
            </div>
            <div class="list_main">
              <div class="small_title">
                <p></p>
                <span @click="change_qes(i)"></span>
              </div>
              <div class="list_main_content">
                <ul>
                  <li v-for="(n_1,i_1) in alike_qes_five[i]" :key="i_1" :title="n_1.question">
                    {{n_1.question}}
                  </li>
                </ul>
              </div>
            </div>
            <div class="list_bottom">
              <div class="small_title">
                <p>FAQ</p>
              </div>
              <div class="list_bottom_content">
                <ul>
                  <li v-for="n_2 in n.faq_similar" :key="n_2.id" :title="n_2.question">
                    <span>[{{n_2.class}}]</span>{{n_2.question}}
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </li>
      </template>
    </ul>
    <div class="fade_form" v-if="show_fade" @click.stop="show_fade = !show_fade">
      <template v-if="modal_type=='add'">
        <find_form :getData1="send_data_1"></find_form>
      </template>
      <template v-else>
        <merge_form ref="merge_form" :getData="send_data"></merge_form>
      </template>
    </div>
    <div class="pagination_bottom">
      <el-pagination background @current-change="handleCurrentChange" :current-page="currentPage4" :page-size="8" layout="total, prev, pager, next, jumper" :total="all_num">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import find_form from "../blocks/find/find_form.vue";
import merge_form from "../blocks/find/merge_form.vue";
export default {
  data() {
    return {
      /* uid */
      /*  */
      alike_qes: [],
      alike_qes_five: [],
      alike_qes_index: [0, 0, 0, 0, 0, 0, 0, 0],
      /*  */
      send_data: {
        id: 0,
        title: "",
        all_similar: []
      },
      send_data_1: {
        id: 0,
        select_data: {},
        title: "",
        all_similar: []
      },
      all_num: 0,
      count_begin: 0,
      count_end: 5,
      articles: [],
      modal_type: "add",
      all_lists: [0, 1, 2, 3, 4, 5, 6, 7],
      ChangeTime: [],
      currentPage4: 1,
      show_fade: false,
      car_data: [],
      car_data_delet: [],
      pickerOptions2: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  computed: {},
  created() {},
  mounted() {
    // setTimeout(() => {

    // }, 2000);
    this.init_home_Data();
  },

  watch: {
    ChangeTime(cur, old) {
      // console.log(cur, old);
      if (cur) {
        var s_t = this.ChangeTime[0];
        var e_t = this.ChangeTime[1];
        this.init_home_Data({ start_time: s_t, end_time: e_t });
      } else {
        this.init_home_Data({});
      }
    }
  },
  methods: {
    getChangeTime() {
      console.log(this.ChangeTime);
      var s_t = this.ChangeTime[0];
      var e_t = this.ChangeTime[1];
      this.init_home_Data({ start_time: s_t, end_time: e_t });
    },
    init_home_Data(
      params = {
        start_time: this.ChangeTime[0],
        end_time: this.ChangeTime[1],
        page: this.currentPage4
      }
    ) {
      /*  */
      const loading = this.$loading({
        lock: true,
        // fullscreen:false,
        text: "ing",
        spinner: "el-icon-loading",
        background: "rgba(255, 255, 255, 0.7)",
        target: document.querySelector(".find")
      });
      this.$cm.ajax(
        "POST",
        "Faq.faq_cluster",
        params,
        res => {
          // console.log(res, 555555);
          this.car_data = res.data.result.data;
          this.car_data_delet = res.data.result.del_id;
          this.all_num = res.data.result.num - 0;
          this.init_five_similar();
          loading.close();
        },
        this
      );
    },
    init_five_similar() {
      this.alike_qes = [];
      for (var i = 0; i < this.car_data.length; i++) {
        this.alike_qes.push(this.car_data[i].similar);
        for (let j = 0; j < this.alike_qes[i].length; j++) {
          if (this.alike_qes[i].length) {
            this.alike_qes_five[i] = this.alike_qes[i].slice(0, 5);
            // this.$set(this.alike_qes_five,i,this.alike_qes[i].slice(0, 5));
          }
        }
      }
    },
    /*  */
    change_qes(index) {
      this.alike_qes_index[index]++;
      if (this.alike_qes_index[index] * 5 > this.alike_qes[index].length) {
        this.alike_qes_index[index] = 0;
      }
      this.$set(
        this.alike_qes_five,
        index,
        this.alike_qes[index].slice(
          this.alike_qes_index[index] * 5,
          this.alike_qes_index[index] * 5 + 5
        )
      );
      // console.log(this.alike_qes_index,this.alike_qes_five,this.alike_qes);
    },
    goBack() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
    },
    /*  */
    dele_card(id) {
      console.log(id);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$cm.ajax(
            "POST",
            "Faq.faq_clusterdelete",
            { uid: this.$cm.uid, id: Number(id) },
            res => {
              console.log(res);
              this.init_home_Data();
            },
            this
          );
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    add_card(question_p, similar_p, question_id) {
      this.modal_type = "add";
      this.show_fade = true;
      // console.log(question_p,similar_p);
      var temp_similar_p = similar_p;
      if (temp_similar_p[0].question != question_p) {
        temp_similar_p.unshift({ id: question_id, question: question_p });
      }
      this.send_data_1 = {
        id: question_id,
        title: question_p,
        all_similar: temp_similar_p
      };
    },
    merge_card(question_p, similar_p, question_id) {
      this.modal_type = "merge";
      this.show_fade = true;
      // console.log(question_p,similar_p);
      var temp_similar_p = similar_p;
      if (temp_similar_p[0].question != question_p) {
        temp_similar_p.unshift({ id: question_id, question: question_p });
      }
      this.send_data = {
        id: question_id,
        title: question_p,
        all_similar: temp_similar_p
      };
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      // console.log(ChangeTime);
      this.currentPage4 = val;
      this.init_home_Data({
        start_time: this.ChangeTime[0],
        end_time: this.ChangeTime[1],
        page: this.currentPage4
      });
      // this.$cm.ajax(
      //   "POST",
      //   "Faq.faq_cluster",
      //   { page: val },
      //   res => {
      //     console.log(res, 555555);
      //     this.car_data = res.data.result.data;
      //     // this.all_num = res.data.result.num-0;
      //     console.log(this.car_data);
      //   },
      //   this
      // );
      // console.log(`: ${val}`);
    }
  },
  components: {
    find_form,
    merge_form
  }
};
</script>
<style lang="less" scoped>
@color: #409eff;
.text_es() {
  overflow: hidden;
  text-overflow: ellipsis;
  /* */
  display: -webkit-box;
  /* */
  -webkit-line-clamp: 1;
  /*  */
  -webkit-box-orient: vertical;
}
h3 {
  font-size: 14px;
}

.find {
  background-color: #f8f9fb;
  padding: 22px 50px;
  font-size: 12px;
  color: #333333;
  display: flex;
  flex-direction: column;
  .dateChange {
    text-align: right;
    padding-right: 20px;
  }
}
.ul_wrapper {
  flex: 1;
  // min-height: 640px;
  // max-height: 740px;
  // min-width: 900px;
  // overflow: auto;
  overflow-x: hidden;
  overflow-y: auto;
}
.list_group {
  width: 25%;
  padding: 15px;
  float: left;
  background-color: #f8f9fb;
  &:hover {
    // transform: scale(1.02);
    .list_wrapper {
      box-shadow: 2px 2px #ccc;
    }
  }
}
.list_wrapper {
  height: 100%;
  width: 100%;
  background-color: #ffffff;
  border-radius: 10px;
  .list_top {
    padding: 16px 15px 16px 20px;
    border-bottom: 1px solid #f5f5f5;
    display: flex;
    align-items: flex-end;
    span {
      display: flex;
      height: 100%;
      justify-content: flex-end;
    }
    i {
      display: block;
      color: @color;
      font-size: 16px;
      cursor: pointer;
      padding: 5px;
      height: 100%;
    }
    h3 {
      width: 90%;
      height: 26px;
      line-height: 26px;
      .text_es();
    }
  }
  .list_main {
    padding: 0px 20px 12px 20px;
    border-bottom: 1px solid #f5f5f5;
    height: 175px;
    .list_main_content {
      ul {
        li {
          line-height: 24px;
          height: 24px;
          color: #999999;
          overflow: hidden;
          text-overflow: ellipsis;
          /* */
          display: -webkit-box;
          /* */
          -webkit-line-clamp: 1;
          /*  */
          -webkit-box-orient: vertical;
        }
      }
    }
  }
  .list_bottom {
    padding: 0px 20px;
    height: 98px;
    .small_title {
      padding: 12px 0px 10px 0px;
    }
    .list_bottom_content {
      ul {
        padding-bottom: 10px;
        li {
          // max-height: 20px;
          margin-top: 4px;
          background-color: #f2f5f9;
          border-radius: 10px;
          color: #999999;
          height: 24px;
          line-height: 24px;
          .text_es();
          span {
            margin: 0 8px;
          }
        }
      }
    }
  }
  .small_title {
    padding: 14px 0px 12px 0px;
    display: flex;
    justify-content: space-between;
    & > span {
      color: @color;
      cursor: pointer;
    }
  }
}
//
.pagination_bottom {
  text-align: right;
  padding-right: 20px;
  padding-top: 36px;
}
</style>




