<template>
  <div class="expand" ref="treeData">
    <h4 @click="init_parent">({{allNumber}})
      <i class="el-icon-plus" @click.stop="handleAddTop" title=""></i>
    </h4>
    <div>
        <el-tree ref="expandMenuList" class="expand-tree"
        v-if="isLoadingTree"
        :data="setTree"
        node-key="ProSort"
        highlight-current
        accordion
        :props="defaultProps"
        :expand-on-click-node="false"
        :render-content="renderContent"
        :default-expanded-keys="defaultExpandKeys"
        @node-click="handleNodeClick"
        @node-expand="getClass"></el-tree>
      </div>
  </div>
</template>
<!-- VUE -->
<script>
import TreeRender from "./tree_render";
export default {
  name: "tree",
  data() {
    return {
      maxexpandId: 99999, //id
      non_maxexpandId: 99999, //id()
      isLoadingTree: false, //
      setTree: [], //
      defaultProps: {
        children: "children",
        label: "name"
      },
      allNumber: 0,
      defaultExpandKeys: [], //
      current_tree_value: 5
    };
  },
  watch: {
    // setTree: {
    //   hanlder(cur, old) {
    //     console.log(cur, old);
    //     this.init_tree_data();
    //   },
    //   deep:true
    // }
    setTree(cur, old) {
      // console.log(cur, old);
    }
  },
  created() {
    this.initExpand();
    this.init_tree_data();
  },
  mounted() {
    // console.log(api);
    // this.$emit('alert');
  },
  methods: {
    //id
    init_parent() {
      this.$parent.init_home_data();
      this.init_tree_data();
      this.initExpand();
      this.$parent.mbxContent = [];
      this.$parent.search_value = '';
      this.$parent.curID = '';
    },
    initAjax(type, url, params, fn) {
      this.$http({
        method: type, //"POST"
        url: this.$cm.baseUrl + url, //
        params: params,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
        // aiic99e4sv2ehmrds2tagq9m36
        // body: {
        //   account: this.form.name,
        //   password: this.form.password,
        //   vcode: this.form.code
        // },
        // emulateJSON: true
      })
        .then(function(res) {
          fn(res);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    init_tree_data() {
      this.$http({
        method: "POST",
        url: this.$cm.baseUrl + "Faq.faq_getclass",
        params: {},
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      })
        .then(function(res) {
          this.allNumber = res.data.result.all_num;
          this.setTree = res.data.result.data.treelist;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    initExpand() {
      this.setTree.map(a => {
        // this.defaultExpandKeys.push(a.id)
      });
      this.isLoadingTree = true;
    },
    handleNodeClick(d, n, s) {
      //
      console.log(d, n, s);
      d.isEdit = false; //
      let that = this;
      this.initAjax("GET", "Faq.faq_getquestion", { id: d.id }, function(res) {
        that.$parent.faq_ques = res.data.result.data;
        that.$parent.num = Number(res.data.result.num);
        that.$parent.state_value = 0;
        that.$parent.search_value = '';
      });

      this.$emit("id", d);
      this.$emit("node", n);
    },
    getClass(d, n, s) {
      this.$emit("id", d);
    },
    renderContent(h, { node, data, store }) {
      //
      let that = this;
      return h(TreeRender, {
        props: {
          DATA: data,
          NODE: node,
          STORE: store,
          maxexpandId: that.non_maxexpandId
        },
        on: {
          nodeAdd: (s, d, n) => that.handleAdd(s, d, n),
          nodeEdit: (s, d, n) => that.handleEdit(s, d, n),
          nodeDel: (s, d, n) => that.handleDelete(s, d, n)
        }
      });
    },
    handleAddTop() {
      this.setTree.unshift({
        id: ++this.maxexpandId,
        count: 0,
        name: "",
        pid: "",
        isEdit: true,
        children: []
      });
      this.setTree.count++;
    },
    handleAdd(s, d, n) {
      //
      console.log(s, d, n);
      if (n.level >= 6) {
        this.$message.error("");
        return false;
      }
      // this.maxexpandId = d.id;
      //
      d.children.unshift({
        id: ++this.maxexpandId,
        name: "",
        pid: d.id,
        isEdit: true,
        children: []
      });
      //
      if (!n.expanded) {
        n.expanded = true;
      }
    },
    handleEdit(s, d, n) {
      //
      console.log(s, d, n);
      if (d.name.length) {
        console.log();
        d.name = d.name;
      }
    },
    handleDelete(s, d, n) {
      //
      console.log(s, d, n);
      let that = this;
      //
      if (d.children && d.children.length !== 0) {
        this.$message.error("");
        return false;
      } else {
        //
        let delNode = () => {
          let list = n.parent.data.children || n.parent.data, //
            _index = 99999; //index
          /*if(!n.parent.data.children){//children
              list = n.parent.data
            }*/
          list.map((c, i) => {
            if (d.id == c.id) {
              _index = i;
            }
          });
          let k = list.splice(_index, 1);
          //console.log(_index,k)
          this.$message.success("");
        };
        let isDel = () => {
          that
            .$confirm("", "", {
              confirmButtonText: "",
              cancelButtonText: "",
              type: "warning"
            })
            .then(() => {
              delNode();
              var param = { id: d.id };
              console.log(param);
              this.$cm.ajax(
                "POST",
                "Faq.faq_deleteclass",
                param,
                res => {
                  console.log(res);
                },
                this
              );
              // this.init_tree_data();
            })
            .catch(() => {
              return false;
            });
        };
        //
        d.id > this.non_maxexpandId ? isDel() : isDel();
      }
    }
  }
};
</script>

<style lang="less">
@color: #4fb4ff;
.expand {
  width: 100%;
  height: 100%;
  /* overflow:auto; */
  h4 {
    vertical-align: middle;
    padding: 0;
    margin: 0;
    padding: 14px 0px 12px 0px;
    cursor: pointer;
    i {
      float: right;
      margin-right: 20px;
      margin-top: 2px;
      cursor: pointer;
      &:hover {
        color: @color;
      }
    }
  }
}
.expand > div {
  /* height:85%; */
  /* padding-top:20px; */
  width: 100%;
  /* margin:20px auto; */
  /* max-width:400px; */
  /* overflow:scroll; */
}
.expand-tree {
  border: none;
  margin-top: 10px;
}
.expand-tree .el-tree-node.is-current,
.expand-tree .el-tree-node:hover {
  overflow: hidden;
}
.expand-tree .is-current > .el-tree-node__content .tree-btn,
.expand-tree .el-tree-node__content:hover .tree-btn {
  display: inline-block;
}
.expand-tree .is-current > .el-tree-node__content .tree-label {
  font-weight: 600;
  white-space: normal;
}
</style>


