<template>
  <div id="words_manager">
    <div class="words-handle">
      <div class="button-grounp">
         <el-button type="primary" @click="add_new()"></el-button>
         <el-button type="danger" @click="delete_some(multipleSelection)"></el-button>
      </div>
      <div class="search-box">
        <el-input placeholder="QID" v-model="searchName" class="input-with-select">
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </div>
    </div>
    <div class="words-table">
      <template>
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="light"
          style="width: 100%;background-color:#f7f7f7;"        
          @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            label=""
            width="120">
          </el-table-column>
          <el-table-column
            label="QID"
            width="144"
            prop="qid"
            >
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            width="120">
          </el-table-column>
          <el-table-column
            prop="other"
            label=""
            show-overflow-tooltip>
            <template slot-scope="scope">
             <span class="other-words-in-list" v-for="(item,index) in scope.row.other" :key="index">{{item}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="weight"
            label=""
            width="134"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            fixed="right"
            label=""
            width="100">
            <template slot-scope="scope">
              <el-button @click="handleClick_edit(scope.row,scope.$index)" type="text" icon="el-icon-edit-outline"></el-button>
              <el-button @click="handleClick_delete(scope.row,scope.$index,tableData)" type="text" icon="el-icon-delete"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <div class="pagination_bottom">
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page="params.page"
          :page-size="setting.pageNum"
          layout="total, prev, pager, next, jumper"
          :total="setting.all_lists_length">
        </el-pagination>
    </div>
    <transition name="el-fade-in-linear">   
    <div class="fade_form" v-if="show_fade" @click.stop="show_fade = !show_fade">
      <words_manager_form :getData="send_data"></words_manager_form>
    </div>
    </transition>   
  </div>  
</template>
<script>
import words_manager_form from "../blocks/words_manager/words_manager_form";
export default {
  name: "words_manager",
  data() {
    return {
      show_fade: false,
      searchName: "",
      params: {
        keyword: this.searchName,
        page: 1,
        limit: 10
      },
      setting: {
        all_lists_length: 100,
        pageNum: 10
      },
      send_data: {},
      tableData: [
        {
          qid: "",
          name: "",
          other: [],
          weight: 1
        }
      ],
      multipleSelection: []
    };
  },
  watch: {
    searchName(cur, old) {
      this.params.keyword = cur;
      this.params.page = 1;
      this.init_table_data(this.params);
    }
  },
  mounted() {
    this.init_table_data(this.params);
  },
  methods: {
    init_table_data(params) {
      this.$cm.ajax(
        "POST",
        "Faq.faq_word",
        params,
        res => {
          // console.log(res);
          this.tableData = res.data.result.data;
          this.setting.all_lists_length = parseInt(res.data.result.num);
        },
        this
      );
    },
    add_new() {
      this.show_fade = true;
      this.send_data = {
        qid: "",
        name: "",
        other: [],
        weight: 1
      };
    },
    delete_some(data) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          var params = { qid: data };
          this.$cm.ajax(
            "POST",
            "Faq.faq_deleteword",
            params,
            res => {
              this.init_table_data(this.params);
            },
            this
          );
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    handleCurrentChange(val) {
      this.params.page = val;
      this.init_table_data(this.params);
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      var praArray = val.map(item => {
        return item.qid;
      });
      this.multipleSelection = praArray;
      console.log(this.multipleSelection);
    },
    handleClick_edit(data) {
      console.log(data);
      this.send_data = data;
      this.show_fade = true;
    },
    handleClick_delete(data) {
      console.log(data);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          var params = { qid: data.qid };
          this.$cm.ajax(
            "POST",
            "Faq.faq_deleteword",
            params,
            res => {
              this.init_table_data(this.params);
              this.$message({
                type: "success",
                message: "!"
              });
            },
            this
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  components: {
    words_manager_form
  }
};
</script>
<style lang="less">
@line: 1px solid #dddddd;
@color: #4fb4ff;
#words_manager {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  padding: 12px 20px 12px 20px;

  // background-color: #f90;
  .words-handle {
    height: 36px;
    position: relative;
    .button-grounp {
      float: left;
      .el-button {
        height: 100%;
        min-width: 100px;
        padding: 0;
        line-height: 36px;
      }
    }
    .search-box {
      position: absolute;
      right: 0;
      bottom: 0;
      border: @line;
      overflow: hidden;
      border-radius: 14px;
      height: 28px;
      width: 338px;
      .el-input__inner {
        border-radius: 0;
        border: none;
      }
      .el-input-group__append {
        background-color: #fff;
        border-radius: 0;
        border: none;
      }
    }
  }
  .words-table {
    flex: 1;
    min-height: 484px;
    overflow: auto;
    padding-top: 12px;
    .el-table {
      border: @line;
      th,
      td {
        padding: 0;
        height: 44px;
      }
      td {
        padding: 0;
        font-size: 12px;
        border: none;
        .el-button--text {
          color: #333;
          font-size: 16px;
          &:hover {
            color: @color;
          }
        }
        .other-words-in-list {
          padding-right: 25px;
          color: #333;
        }
        .cell {
          color: #333;
        }
      }
      th {
        color: #151515;
        border-bottom: @line;
      }
      img {
        height: 16px;
        width: 16px;
      }
      .el-checkbox__input.is-indeterminate .el-checkbox__inner {
        background-color: #fff;
        border-color:#dcdfe6;
      }
      .el-table-column--selection {
        text-align: center;
      }
      .hover-row {
        background-color: #f7f7f7;
        &:hover {
          background-color: #f7f7f7;
        }
      }
    }
  }
  .pagination_bottom {
    text-align: right;
  }
}
</style>




