<template>
  <div id="three-table">
    <template>
      <el-table
          :data="tableData"
          style="width: 100%"
          @sort-change="sortTable"
          >
          <el-table-column          
            prop="question"
            label="FAQ"
            >
          </el-table-column>
          <el-table-column            
              prop="ask"
              sortable = "custom"
              label=""
              width="150"
              >
          </el-table-column>
          <el-table-column label="">
              <el-table-column
              prop="rated"
              sortable = "custom"                    
              label=""
              width="105">
              </el-table-column>
              <el-table-column
              prop="favorite"
              sortable = "custom"                    
              label=""
              width="105">
              </el-table-column>
              <el-table-column
              prop="favorite_percent"
              sortable = "custom"                    
              label=""
              width="105">
              </el-table-column>
              <el-table-column
              prop="nolike"
              sortable = "custom"                    
              label=""
              width="105">
              </el-table-column>
              <el-table-column
              prop="nolike_percent"
              sortable = "custom"                    
              label=""
              width="105">
              </el-table-column>
          </el-table-column>
          <el-table-column label="">
              <el-table-column
              prop="people"
              sortable = "custom"                    
              label=""
              width="120">
              </el-table-column>
              <el-table-column
              prop="people_percent"
              sortable = "custom"                    
              label=""
              width="120">
              </el-table-column>
          </el-table-column>
      </el-table>
    </template>
    <div class="pagination_bottom">
      <el-pagination
        background
        accordion
        @current-change="handleCurrentChange"
        :current-page="page_detail.currentPage"
        :page-size="page_detail.size"
        layout="total, prev, pager, next, jumper"
        :total="page_detail.num">
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  name: "three-table",
  data() {
    return {
      //
      page_detail: {
        currentPage: 1,
        size: 9,
        num: 100
      },
      params: {
        sort_type: 1,
        sort_data: 2,
        page: 1,
        faq_id: ""
      },
      tableData: [],
      faq_id: 0,
      save_props: {}
    };
  },
  props: ["getIdPage"],
  created() {
    console.log(this.getIdPage);
    this.save_props = this.getIdPage;
    this.getTableData(this.params);
  },
  watch: {
    save_props: {
      handler(cur) {
        this.params.faq_id = cur.faq_id;
      },
      deep: true
    },
    params: {
      handler(cur) {
        this.params.faq_id = cur.faq_id;
        this.getTableData(cur);
      },
      deep: true
    }
  },
  methods: {
    getTableData(params) {
      this.$cm.ajax(
        "POST",
        "Faq.faq_totaldata",
        params,
        res => {
          this.tableData = res.data.result.data;
          this.page_detail.num = Number(res.data.result.num);
        },
        this
      );
    },
    handleCurrentChange(val) {
      console.log(val);
      this.params.page = val;
      // this.sendId_page.page = val;
    },
    sortTable(column) {
      console.log(column, 8999);
      var params = this.params;
      console.log(column.order);
      if (column.order == "descending") {
        params.sort_data = 2;
      } else {
        params.sort_data = 1;
      }
      switch (column.prop) {
        case "ask":
          params.sort_type = 1;
          break;
        case "rated":
          params.sort_type = 2;
          break;
        case "favorite":
          params.sort_type = 3;
          break;
        case "favorite_percent":
          params.sort_type = 4;
          break;
        case "nolike":
          params.sort_type = 5;
          break;
        case "nolike_percent":
          params.sort_type = 6;
          break;
        case "people":
          params.sort_type = 7;
          break;
        case "people_percent":
          params.sort_type = 8;
          break;
        default:
          params.sort_type = 1;
          break;
      }
      console.log(params);
      this.getTableData(params);
    }
  }
};
</script>

<style lang="less">
#three-table {
  flex: 1;
  padding-top: 40px;
  // width: 100%;
  // background-color: #096;
  .el-table td,
  .el-table th {
    padding: 8px 0;
    // background-color: #fff;
    font-size: 14px;
    color: #151515;
  }
  .el-table td {
    font-size: 12px;
    color: #333333;
    max-height: 40px;
  }
  .pagination_bottom {
    padding-top: 10px;
  }
}
</style>




