<template>
  <div class="first">
      <ul>
          <li class="logo">
              <p>TalkingData</p>
          </li>
          <li class="avatar">
            <img src="../../assets/logo.png" alt="" @click.stop="show_list">
            <ul class="manager_info" v-show="show_link">
              <li @click="show_link=false">
              <router-link :to="{ path: '/info' }"></router-link>
              </li>
              <li @click="out_system">
                <a></a>
              </li>
            </ul>
          </li>
      </ul>     
  </div>
</template>
<script>
export default {
  name: "nav_header",
  data() {
    return {
      user_name: "hyf"
    };
  }
};
</script>
<style lang="less" scoped>
@line:1px solid #dcdfe6;
@color:#4fb4ff;
.first {
  background-color: #ffffff;
  &>ul{
    display: flex;
    width: 100%;
    min-width:300px;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #dddddd;
    &>li {
      display: flex;
      align-items: center;
      padding: 6px 20px;
      p {
        font-size: 16px;
        font-weight: bold;
      }
    }
    .avatar {
      position: relative;
      img {
        border:@line;
        border-radius: 50%;
        width:40px;
        height:40px;
        object-fit: cover;
      }
      .manager_info {
        position: absolute;  
        left:-60px;
        top:30px;
        // height:56px;
        width:72px;
        border: @line;
        z-index:10;
        border-radius: 12px 2px 2px 2px;
        overflow: hidden;
        background-color: #ffffff;
        li>a {
          width:100%;
          font-size: 12px;
          padding:0px 10px;
          line-height: 32px;
          color:#999999;
          cursor: pointer;
          background-color: #ffffff;
          &:hover {
            color:@color;
          }
        }
      }
    }
  }
}
</style>
<script>
  export default {
    data(){
      return {
        show_link:false
      }
    },
    methods:{
      out_system(){
        // this.$routes.push('/login');
        this.$parent.login_show = true;
        this.$router.push({path: '/login', query: {selected: "2"}});
        this.$cm.clearCookie();
      },
      show_list(){
        this.show_link = !this.show_link;
        var that = this;
        document.body.addEventListener('click',function(){
          that.show_link = false;
        })
      }
    }
  }
</script>




