<template>
	<span class="tree-expand">
		<span class="tree-label" v-show="DATA.isEdit">
			<el-input class="edit" size="mini" autofocus
			v-model="DATA.name"
			:ref="'treeInput'+DATA.id"
			@click.stop.native="nodeEditFocus"
			@blur.stop="nodeEditPass(STORE,DATA,NODE)">
      </el-input>
			<!-- @keyup.enter.stop.native="nodeEditPass(STORE,DATA,NODE)" -->
		</span>
		<span v-show="!DATA.isEdit" :class="[DATA.id > maxexpandId ? 'tree-new tree-label' : 'tree-label']">
            <template v-if="DATA.count">
			    <span :title="DATA.name">{{DATA.name}}({{DATA.count}})</span>
            </template>
            <template v-else>
			    <span :title="DATA.name">{{DATA.name}}</span>
            </template>
		</span>
		<span class="tree-btn" v-show="!DATA.isEdit">
			<i class="el-icon-plus" @click.stop="nodeAdd(STORE,DATA,NODE)" title=""></i>
			<i class="el-icon-edit" @click.stop="nodeEdit(STORE,DATA,NODE)" title=""></i>
			<i class="el-icon-delete" @click.stop="nodeDel(STORE,DATA,NODE)" title=""></i>
		</span>
	</span>
</template>

<script>
export default {
  name: "treeExpand",
  props: ["NODE", "DATA", "STORE", "maxexpandId"],
  methods: {
    nodeAdd(s, d, n) {
      //
      // console.log(s,d,n)
      this.$emit("nodeAdd", s, d, n);
      // if(d.name.length===0){
      // 	d.name = ''
      // }else {
      // 	console.log(s,d,n);
      // 	var param={
      // 		parent_id:d.pid-0,
      // 		c_name:d.name
      // 	};
      // 	this.$cm.ajax("POST","Faq.faq_newclass",param,(res)=>{console.log(res)},this)
      // }
    },
    nodeEdit(s, d, n) {
      //
      d.isEdit = true;
      this.$nextTick(() => {
        this.$refs["treeInput" + d.id].$refs.input.focus();
      });
      this.$emit("nodeEdit", s, d, n);
    },
    nodeDel(s, d, n) {
      //
      this.$emit("nodeDel", s, d, n);
    },
    nodeEditPass(s, d, n) {
      //
      d.isEdit = false;
      if (d.name.length === 0) {
        d.name = "";
      }
      if (!d.ProSort) {
        console.log(d.id);
        var param = {
          parent_id: d.pid - 0,
          c_name: d.name
        };
        this.$cm.ajax(
          "POST",
          "Faq.faq_newclass",
          param,
          res => {
            console.log(res);
            d.id = res.data.result.id - 0;
          },
          this
        );
      }
      if (d.ProSort) {
        console.log(d.id);
        var param = {
          id: d.id - 0,
          c_name: d.name
        };
        this.$cm.ajax(
          "POST",
          "Faq.faq_editclass",
          param,
          res => {
            console.log(res);
          },
          this
        );
        console.log(this, this.$parent, this.refs);
        // this.$parent.init_tree_data();
      }
    },
    nodeEditFocus() {
      //
    }
  }
};
</script>

<style>
.tree-expand {
  display: flex;
  overflow: hidden;
  width: 100%;
  height: 100%;
  padding: 0px 0px;
  line-height: 26px;
}
.tree-expand .tree-label.tree-new {
  font-weight: 600;
}
.tree-expand .tree-label {
  font-size: 0.9em;
  flex: 1;
  height: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.tree-expand .tree-label .edit {
  width: 80%;
}
.tree-expand .tree-btn {
  display: none;
  float: right;
  margin-right: 20px;
}
.tree-expand .tree-btn i {
  color: #24C79E;
  font-size: 0.9em;
  margin-right: 3px;
}
</style>


