<template>
  <div class="echarts">
    <div class="all_charts">
        <div class="top_chart">
            <div class="count" :style="{width: '50%', height: '100%'}">
                <div class="title">
                    <div class="txt_title">
                        
                    </div>
                    <div class="tips_title">
                        <ul>
                            <li>
                                <span class="color_tips" style="background-color:#4DCFC5"></span>
                            
                                <p></p>
                            </li>
                            <li>
                                <span class="color_tips" style="background-color:#A489D8"></span>
                                <p></p>
                            </li>
                            <li>
                                <span class="color_tips" style="background-color:#E9E9E9"></span>
                                <p>FAQ</p>
                            </li>
                        </ul>
                    </div>
                </div>
                <div id="count" class="chart_content"></div>
            </div>
            <div class="question" :style="{width: '50%', height: '100%'}">
                <div class="title">
                    <div class="txt_title">
                        /
                    </div>
                    <div class="tips_title">
                        <ul>
                            <li>
                                <span class="color_tips" style="background-color:#4DCFC5"></span>
                                <i></i>
                                <p></p>
                            </li>
                            <li>
                                <span class="color_tips" style="background-color:#A489D8"></span>
                                <p></p>
                            </li>
                            <li>
                                <span class="color_tips" style="background-color:#E9E9E9"></span>
                                <p>FAQ</p>
                            </li>
                        </ul>
                    </div>
                </div>
                <div id="que_number" class="chart_content"></div>
            </div>
        </div>
        <div class="down_charts">
          <div class="title_data">
            <div class="title" style="">
              <div class="txt_title" style="width:120px">
                  
              </div>
              <div class="tips_title" style="width:45%">
                  <ul >
                      <li v-for=" (item,index) in bar_data['color']" :key=index>
                          <span class="color_tips" v-bind:style="{background : item}"></span>                         
                          <p>{{bar_data.Axis[index]}}</p>
                      </li>
                  </ul>
              </div>
            <div class="data_picker" style="width:50%">
            <el-date-picker
              v-model="ChangeTime"
              size="mini"
              type="daterange"
              align="right"
              unlink-panels
              range-separator="-"
              start-placeholder=""
              end-placeholder=""
              format="yyyy / MM / dd "
              value-format="yyyy-MM-dd"
              :picker-options="pickerOptions2">
            </el-date-picker>
            </div>
          </div>
          </div>
          <div class="main_chart">
            <div class="main_bar">
              <div id="date_bar" style="height:100%;width:100%"></div>
            </div>
            <div class="main_tips">
              <h2></h2>
              <ul>
                <li>
                  <p></p>
                  <span>{{data_statistic.ask_num}}</span>
                </li>
                <li>
                  <p></p>
                  <span>{{data_statistic.user_num}}</span>
                </li>
                <li>
                  <p></p>
                  <span>{{data_statistic.turn_num}}</span>
                </li>
                <li>
                  <p></p>
                  <span>{{data_statistic.point_num}}</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
        
    </div>
  </div>
</template>
<script>
let myChart, myChart1, myChart2;
export default {
  name: "home",
  data() {
    return {
      articles: [],
      pickerOptions2: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      chart_color: ["#4DCFC5", "#A489D8", "#E9E9E9"],
      bar_data: {
        color: ["#A2D5F9", "#FAB2D0", "#85E2DA", "#E1D2B6", "#FED092"],
        Axis: []
      },
      data_statistic: {},
      ChangeTime: []
    };
  },
  watch: {
    ChangeTime(cur, old) {
      console.log(cur);
      if (cur) {
        var s_t = this.ChangeTime[0];
        var e_t = this.ChangeTime[1];
        this.init_home_Data({ start_time: s_t, end_time: e_t });
      } else {
        this.init_home_Data({});
      }
    }
  },
  created() {},
  mounted() {
    myChart = this.$echarts.init(document.getElementById("count"));
    myChart1 = this.$echarts.init(document.getElementById("que_number"));
    myChart2 = this.$echarts.init(document.getElementById("date_bar"));

    window.addEventListener('resize',function(){
      myChart.resize();
      myChart1.resize();
      myChart2.resize();
    });
    this.$nextTick(function() {
      var _this = this;
      _this.init_data({ type: 1 }, function(res) {
        _this.drawPie(res.data.result.data);
      });
      _this.init_data({ type: 2 }, function(res) {
        _this.drawLine(res.data.result.Axis, res.data.result.data);
      });
      _this.init_data({ type: 3 }, function(res) {
        _this.bar_data.Axis = res.data.result[0].Axis;
        _this.drawBar(res.data.result[0].Axis, res.data.result[0].data);
      });
      _this.init_data({ type: 4 }, function(res) {
        _this.data_statistic = res.data.result;
      });
    });
  },
  computed: {
    username() {
      //  `params` 
      return this.$route.params.username;
    }
  },
  methods: {
    init_data(params, fn) {
      this.$http({
        method: "POST",
        url: this.$cm.baseUrl + "Faq.faq_total",
        params: params,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
        // aiic99e4sv2ehmrds2tagq9m36
        // body: {
        //   account: this.form.name,
        //   password: this.form.password,
        //   vcode: this.form.code
        // },
        // emulateJSON: true
      })
        .then(function(res) {
          fn(res);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    goBack() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
    },
    drawPie(data) {
      // domecharts
      // 
      let _this = this;
      myChart.setOption({
        title: {
          text: "",
          left: "center",
          top: "center",
          textStyle: {
            color: "#999999",
            fontSize: "30px",
            fontWeight: "normal"
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        color: ["#4DCFC5", "#A489D8", "#E9E9E9"],
        backgroundColor: "#ffffff",
        series: [
          {
            name: "",
            type: "pie",
            radius: ["50%", "70%"],
            backgroundColor: "#ffffff",
            // avoidLabelOverlap: false,
            label: {
              normal: {
                show: true,
                position: "outside",
                formatter: "{b}:{d}%",
                formatter: "{b|{b}}{a|{d}%}\n\n",
                borderWidth: 20,
                borderRadius: 4,
                // shadowBlur:3,
                // shadowOffsetX: 2,
                // shadowOffsetY: 2,
                // shadowColor: '#999',
                padding: [0, -70],
                rich: {
                  a: {
                    color: "#333",
                    fontSize: 12,
                    lineHeight: 20
                  },
                  b: {
                    fontSize: 12,
                    lineHeight: 20,
                    color: "#333"
                  }
                }
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "20",
                  fontWeight: "bold"
                }
              }
            },
            // label: {
            //   normal: {
            //     show: true,
            //     position: "outside",
            //     formatter: "{b}:{d}%"
            //   }
            // },
            labelLine: {
              normal: {
                length: 20,
                length2: 70,
                lineStyle: {
                  color: "#333"
                }
              }
            },
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            },
            data: data
          }
        ]
      });
    },
    drawLine(axis, data) {
      // domecharts
      // 
      // console.log(this);
      myChart1.setOption({
        tooltip: {
          trigger: "axis"
        },
        xAxis: {
          name: "",
          type: "category",
          boundaryGap: false,
          axisLine: {
            show: false
          },
          data: axis
        },
        yAxis: {
          show: false
        },
        color: ["#4DCFC5", "#A489D8", "#E9E9E9"],
        backgroundColor: "#ffffff",

        series: data
      });
    },
    drawBar(xAxis, data) {
      // domecharts
      // 
      myChart2.setOption({
        title: {
          text: "FAQ",
          left: "30",
          top: "10",
          textStyle: {
            color: "#999999",
            fontSize: "12",
            fontWeight: "normal"
          }
        },
        // color: ["#A2D5F9", "#FAB2D0", "#85E2DA", "#E1D2B6", "#FED092"],
        color: function(params) {
          var colorList = [
            "#A2D5F9",
            "#FAB2D0",
            "#85E2DA",
            "#E1D2B6",
            "#FED092"
          ];
          return colorList[params.dataIndex];
        },
        backgroundColor: "#ffffff",
        tooltip: {},
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          show: false,
          data: xAxis
        },
        yAxis: {
          name: "",
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            show: false
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: ["#f5f5f5"]
            }
          }
        },
        series: [
          {
            name: "FAQ",
            type: "bar",
            data: data,
            itemStyle: {},
            label: {
              normal: {
                show: true, //
                position: "top", //
                formatter: "{c}%"
              }
            },
            barWidth: "36px",
            barGap: "56px"
          }
        ]
      });
    },
    changeDate() {}
  }
};
</script>
<style lang="less" scoped>
.clearfix() {
  &::before,
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}
.ver_middle() {
}

.echarts {
  display: flex;
  flex-direction: column;
  min-height: 600px;
  padding: 40px 48px;
  font-size: 12px;
  color: #333333;
}
.all_charts {
  height: 100%;
  flex-direction: column;
  //
}
.top_chart {
  height: 50%;
  display: flex;
  .question {
    margin-left: 20px;
    width: calc(100% - 20px);
  }
  & > div {
    border-radius: 10px;
    border: 1px solid #cccccc;
    overflow: hidden;
    .chart_content {
      height: 90%;
      width: 100%;
    }
  }
}
.down_charts {
  height: 48.5%;
  margin-top: 5px;
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid #cccccc;
  margin-top: 1.5%;
  .title_data {
    width: 100%;
  }
  .main_chart {
    height: 90%;
    width: 100%;
    display: flex;
    .main_bar {
      width: 50%;
      height: 100%;
    }
    .main_tips {
      width: 50%;
      height: 100%;
      h2 {
        font-size: 12px;
        color: #cccccc;
        height: 10%;
        padding: 12px 0 0 85px;
      }
      ul {
        height: 80%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        li {
          line-height: 34px;
          border-bottom: 1px solid #cccccc;
          width: 80%;
          margin-top: 12px;
          p {
            font-size: 12px;
            color: #333333;
            float: left;
          }
          span {
            font-size: 20px;
            color: #cccccc;
            float: right;
          }
        }
      }
    }
  }
}
.title {
  background-color: rgb(248, 248, 248);
  display: flex;
  height: 40px;
  line-height: 40px;
  .clearfix();
  .txt_title {
    padding: 0px 12px;
    height: 100%;
    justify-content: flex-start;
  }
  .tips_title {
    padding: 0px 12px;
    flex: 1;
    height: 100%;
    & > ul {
      justify-content: flex-end;
      height: 100%;
      display: flex;
      li {
        display: flex;
        align-items: center;
        span {
          display: block;
          height: 10px;
          width: 10px;
          cursor: pointer;
          margin: 0px 10px;
        }
        p {
          color: #666666;
        }
      }
    }
  }
  .data_picker {
    height: 100%;
    text-align: right;
    padding-right: 28px;
  }
}
.main_tips {
  background-color: #fff;
}
</style>




