<template>
  <div class="manager_content">
    <div class="manegerTree">
      <div class="treeTitle">
        <p>FAQ</p>
      </div>
      <div class="treeWrapper">
        <tree v-on:id="getChildId" v-on:node="getChildNode"></tree>
      </div>
      <div class="addFAQ">
        <p @click="add_faq"> + FAQ</p>
        <p class="up-exsel">
          <label for="exl" style="cursor:pointer"></label>
        </p>
        <input type="file" name="" id="exl" style="display:none" @change="upload($event)">
      </div>
    </div>
    <div class="managerTable">
      <div class="nav_search">
        <div class="mianbaoxie">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item></el-breadcrumb-item>
            <template v-for="(item,index) in mbxContent">
              <el-breadcrumb-item :key="index">{{item}}</el-breadcrumb-item>
            </template>
          </el-breadcrumb>
        </div>
        <div class="searchBox">
          <input type="r=text" placeholder="" v-model="search_value" v-on:input="searchList()" />
          <span></span>
        </div>
      </div>
      <div class="list_table yo-table">
        <table>
          <thead>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th>
              <span></span>
              <span class="filter_box">
                <el-select v-model="state_value" placeholder="" @change="slectedStatus()">
                  <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value" :disabled="item.disabled">
                  </el-option>
                </el-select>
              </span>
            </th>
            <th></th>
          </thead>
          <tbody>
            <template v-for="(item,index) in faq_ques">
              <tr :key="item.id" @click.stop="editer_faq(item.id)">
                <td>{{item.id}}</td>
                <td>
                  <div :title="item.question">{{item.question}}</div>
                </td>
                <td class="alike_ques">
                  {{item.alike}}
                  <div class="pointer" @click.stop="alikeNumber(item.id,item.question)">
                  </div>
                </td>
                <td>{{item.end_time.substring(0,10)}}</td>
                <td class="change_box">
                  <p class="watch_log" :class="{'running':item.status == 1,'stop':item.status == 0}">{{item.status == 1 ?"":""}}</p>
                </td>
                <td class="handle_box" @click.stop="stop_temp(item.id)">
                  <p class="watch_log" :class="state_handle===0?'down':'up'">
                  </p>
                  <span class="handle" :class=" active == item.id ? 'slectedActive': '' " @click.stop="change_state(item.id,item.status == '1'?0:1,index)" v-text="item.status == 1 ?'':''"></span>
                </td>
              </tr>
            </template>
          </tbody>
        </table>
      </div>
      <div class="fade_form" v-if="show_fade" @mousedown="hide($event)">
        <template v-if="show_type==='add'">
          <add_form :getmbxContentId="mbxContentId"></add_form>
        </template>
        <template v-else-if="show_type==='editer'">
          <editer_form ref="editer_child" v-bind:slectedId="slectedId" :getData1="send_data_1"></editer_form>
        </template>
        <template v-else-if="show_type==='alike'">
          <alike_form v-bind:alike="allAlick"></alike_form>
        </template>
        <template v-else>
          <manager_form></manager_form>
        </template>
      </div>
      <div class="pagination_bottom">
        <el-pagination background accordion @current-change="handleCurrentChange" :current-page="currentPage4" :page-size="11" layout="total, prev, pager, next, jumper" :total="num">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import manager_form from "../blocks/manager/manager_form.vue";
import add_form from "../blocks/manager/add_form.vue";
import editer_form from "../blocks/manager/editer_form.vue";
import alike_form from "../blocks/manager/alike_form.vue";
import tree from "../blocks/manager/tree.vue";
let id = 1000;

export default {
  data() {
    return {
      send_data_1: {
        id: 0,
        select_data: {},
        title: ""
      },
      active: "",
      slectedId: "",
      all_data: {},
      up_down_btn: false,
      state_handle: 1,
      search_value: "",
      currentPage4: 1,
      defaultProps: {
        children: "children",
        label: "label"
      },
      show_fade: false,
      state: "",
      show_modal: "editer", //
      show_type: "",
      state_value: 0,
      current_tree_value: 0,
      options2: [
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        }
      ],
      faq_ques: "",
      status: "3",
      num: 0,
      curID: "",
      allAlick: {},
      mbxContent: [],
      mbxContentId: []
    };
  },
  watch: {
    state_value(cur, old) {
      this.state = this.state_value == 0 ? "running" : "stop";
    }
  },
  mounted() {
    //
    this.init_home_data();
    /* formselect */
    this.$cm.ajax(
      "POST",
      "Faq.faq_getchooseclass",
      {},
      res => {
        this.send_data_1.select_data = res.data.result.data;
      },
      this
    );
  },
  methods: {
    upload(event) {
      if (event.target.files.length > 0) {
        console.log(event);
        this.$alert('');
      }
    },
    hide(e) {
      console.log(e.target.getAttribute("class"));
      if (e.target.getAttribute("class") == "fade_form") {
        this.show_fade = false;
      }
    },
    init_home_data(id = "", pag = 1) {
      var _this = this;
      _this.initAjax(
        "GET",
        "Faq.faq_getquestion",
        { id: id, page: pag },
        function(res) {
          _this.faq_ques = res.data.result.data;
          _this.num = Number(res.data.result.num);
        }
      );
    },
    //
    slectedStatus() {
      switch (this.state_value) {
        case 0:
          this.status = 3;
          break;
        case 1:
          this.status = 1;
          break;
        case 2:
          this.status = 0;
          break;
      }
      console.log(this.state_value);
      var _this = this;
      this.initAjax(
        "GET",
        "Faq.faq_getquestion",
        { status: _this.status, id: this.curID },
        function(res) {
          _this.faq_ques = res.data.result.data;
          _this.num = Number(res.data.result.num);
        }
      );
    },
    getChildId(data) {
      // console.log('id' + data);
      console.log("data", data);
      // if (data.pid) {
      //   // this.mbxContent.splice(2, 1, data.name);
      //   this.mbxContentId.splice(1, 1, data.id);
      // } else {
      //   // this.mbxContent = ["", data.name];
      //   this.mbxContentId = [data.id];
      // }
      this.curID = data.id;
    },
    getChildNode(data) {
      // console.log("data", data);
      var temp = data;
      var arr = [temp.label];
      var arrID = [temp.key];
      for (var i = 0; i < data.level - 1; i++) {
        temp = temp.parent;
        arr.unshift(temp.label);
        arrID.unshift(temp.key);
      }
      this.mbxContent = arr;
      this.mbxContentId = arrID.join(".");
      // console.log(this.mbxContentId,56555);
    },
    findPro(data, pro, name, lv) {},
    //,
    //TODO:
    searchList() {
      var timeout = null;
      clearTimeout(timeout);
      var _this = this;
      timeout = setTimeout(function() {
        _this.initAjax(
          "GET",
          "Faq.faq_getquestion",
          { keyword: _this.search_value, id: _this.curID },
          function(res) {
            _this.faq_ques = res.data.result.data;
            _this.num = Number(res.data.result.num);
            // _this.currentPage4 = 1;
            // console.log(_this,456);
          }
        );
      }, 300);
    },
    //
    debounce(fn, interval) {
      var _this = this;
      let timeout = null;
      return function() {
        clearTimeout(timeout);
        timeout = setTimeout(() => {
          fn.apply(_this, arguments);
        }, interval);
      };
    },
    //
    initAjax(type, url, params, fn) {
      this.$http({
        method: type, //"POST"
        url: this.$cm.baseUrl + url, //
        params: params,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
        // aiic99e4sv2ehmrds2tagq9m36
        // body: {
        //   account: this.form.name,
        //   password: this.form.password,
        //   vcode: this.form.code
        // },
        // emulateJSON: true
      })
        .then(function(res) {
          fn(res);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //id
    getIDQuestion(id) {
      var _this = this;
    },
    stop_temp(id) {
      this.active = id;
      console.log(this.active);
    },
    //
    change_state(id, line, index) {
      var _this = this;
      _this.faq_ques[index].status = line;
      _this.initAjax("GET", "Faq.faq_line", { id: id, line: line }, function(
        res
      ) {});
    },
    alikeNumber(id, ques) {
      this.show_fade = true;
      this.show_type = "alike";
      this.allAlick.id = id;
      this.allAlick.ques = ques;
    },
    editer_faq(id) {
      this.show_fade = true;
      this.show_type = "editer";
      this.slectedId = id;
    },
    add_faq() {
      this.show_fade = true;
      this.show_type = "add";
    },
    show_alikeNumber_modal_modal() {},
    //
    handleCurrentChange(val) {
      //state_value 0: 1 2
      console.log(this.state_value);
      var _this = this;
      this.initAjax(
        "GET",
        "Faq.faq_getquestion",
        {
          id: this.curID,
          page: val,
          status: _this.status,
          keyword: _this.search_value
        },
        function(res) {
          console.log(res.data.result);
          _this.faq_ques = res.data.result.data;
        }
      );
    },
    append(data) {
      const newChild = { id: id++, label: "testtest", children: [] };
      if (!data.children) {
        this.$set(data, "children", []);
      }
      data.children.push(newChild);
    },
    remove(node, data) {
      const parent = node.parent;
      const children = parent.data.children || parent.data;
      const index = children.findIndex(d => d.id === data.id);
      children.splice(index, 1);
    },
    handleNodeClick(data) {
      console.log(data);
    },
    show_editor(event, data, node, self) {
      console.log(event, data, node, self, "---------158");
    }
  },
  components: {
    manager_form,
    tree,
    add_form,
    editer_form,
    alike_form
  }
};
</script>
<style lang="less" scoped>
.yo-table tr .handle_box .handle.slectedActive {
  display: inline-block;
}
.text_es() {
  overflow: hidden;
  text-overflow: ellipsis;
  /* */
  /* */
  -webkit-line-clamp: 1;
  /*  */
}
/* table */
@line: 1px solid #edf0f5;
@color: #409eff;
@line: 1px solid #edf0f5;
.running,
.wait,
.stop {
  position: relative;
  padding-left: 10px;
  &::after {
    content: "";
    display: block;
    border-radius: 50%;
    position: absolute;
    width: 5px;
    height: 5px;
    top: 7px;
    left: -5px;
    background-color: #08ca71;
  }
}
.wait {
  &::after {
    background-color: #43a4de;
  }
}
.stop {
  &::after {
    background-color: #ff2e29;
  }
}
.yo-table {
  flex: 1;
  table {
    width: 100%;
    // height: 100%;
    border: 1px solid #cccccc;
    border-collapse: collapse;
    font-size: 12px;
  }
  th,
  tr {
    height: 40px;
  }
  th {
    position: relative;
    font-size: 14px;
    font-weight: 600;
    text-align: left;
    color: #151515;
    border-bottom: 1px solid #cccccc;
    &:nth-child(1) {
      padding-left: 38px;
    }
    .filter_box {
      // display: inline-block;
      // width:12px;
      float: left;
      height: 12px;
      background-color: @color;
      vertical-align: middle;
      // margin-left:8px;
      margin-top: 4px;
    }
  }
  .change_box {
    padding-left: 8px;
  }

  tr {
    // padding-left: 38px;
    &:hover {
      background-color: #e4f9ff;
    }
    .handle_box {
      position: relative;
      cursor: pointer;
      .watch_log {
        display: inline-block;
        height: 18px;
        width: 14px;
        border-radius: 50%;
        overflow: hidden;
        background: url(../../assets/guanli_xiaxian.png) no-repeat center center;
        background-size: 100%;
        margin-left: 8px;
      }
      .handle {
        display: none;
        cursor: pointer;
      }

      & > span {
        // position: absolute;
        vertical-align: top;
        // display: none;
        display: inline-block;
        width: 44px;
        height: 18px;
        text-align: center;
        background-color: #fff;
        border-radius: 9px;
        border: @line;
        color: #999999;
        font-size: 10px;
        margin-left: 8px;
      }
      .up {
        background: url(../../assets/guanli_shangxian.png) no-repeat center
          center;
        display: inline-block;
      }
      .down {
        background: url(../../assets/guanli_xiaxian.png) no-repeat center center;
        display: inline-block;
      }
    }
    td {
      div {
        .text_es();
        height: 44px;
        line-height: 44px;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      min-width: 94px;
      &:nth-child(1) {
        padding-left: 38px;
      }
      &:nth-child(2) {
        min-width: 314px;
      }
      &:nth-child(3) {
        min-width: 130px;
      }
      &:nth-child(4) {
        min-width: 166px;
      }
      &:nth-child(5) {
        min-width: 120px;
      }
      &:nth-child(5) {
        min-width: 120px;
      }
    }
  }
}
/*  */
.manager_content {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  .manegerTree {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 284px;
    overflow: hidden;
    border-right: @line;
    color: #4c4c4c;
    & > div {
      padding-left: 20px;
      font-size: 14px;
    }
    .treeTitle {
      height: 50px;
      line-height: 50px;
      border-bottom: @line;
    }
    .treeWrapper {
      flex: 1;
      overflow-x: auto;
      overflow-y: auto;
      & > h4 {
        padding: 14px 0px 4px 0px;
      }
      &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 0px rgb(230, 230, 230);
        border-radius: 5px;
      }
      &::-webkit-scrollbar-thumb {
        background-color: rgb(230, 230, 230);
        outline: 1px solid rgb(230, 230, 230);
        border-radius: 5px;
      }
      &::-webkit-scrollbar {
        width: 10px;
      }
    }
    .addFAQ {
      height: 50px;
      line-height: 50px;
      border-top: @line;
      padding: 0 20px;
      color: @color;
      display: flex;
      justify-content: space-between;
      cursor: pointer;
      // p{
      //   width:50%;
      // }
      .up-exsel {
        label {
          display: block;
          height:100%;
          width:100%;
        }
      }
    }
  }
  .managerTable {
    display: flex;
    flex-direction: column;
    padding: 0px 20px 10px 20px;
    flex: 1;
    color: #333333;
    .nav_search {
      padding: 8px 0px 8px 0px;
      & > div {
        display: inline-block;
      }
      .searchBox {
        float: right;
        min-width:220px;
        height: 28px;
        border-radius: 14px;
        padding-left: 12px;
        overflow: hidden;
        display: flex;
        border: @line;
        input {
          flex: 1;
          height: 100%;
          border: none;
          outline: none;
        }
        span {
          height: 100%;
          width: 28px;
          background: url(../../assets/guanli_search.png) no-repeat center
            center;
          background-size: 65%;
          padding: 2px;
          cursor: pointer;
        }
      }
      .mianbaoxie {
        color: #999999;
        font-size: normal;
      }
    }
    .list_table {
      flex: 1;
      overflow: auto;
      th {
        color: #151515;
        font-size: 14px;
        font-weight: normal;
      }
      .alike_ques {
        position: relative;
        .pointer {
          display: none;
          position: absolute;
          height: 14px;
          width: 14px;
          border: @line;
          overflow: hidden;
          background: url(../../assets/guanli_more.png) no-repeat center center;
          background-size: 100%;
          top: 50%;
          right: 66%;
          transform: translateY(-50%);
          cursor: pointer;
        }
        &:hover {
          .pointer {
            display: block;
          }
        }
      }
    }
    .pagination_bottom {
      text-align: right;
    }
  }
}
</style>





