<template>
    <div class="header sidebar">
        <ul class="sidebar-menu ">`
      <template v-for="item in menulist">
        <li :class="currentUrl === item.menuUrl ? 'active':''" :key="item.menuUrl">
          <template v-if="item.menuUrl">
            <router-link :to="item.menuUrl" @click.native="toggleName=''" exact>
              <i :class="['fzicon',item.menuIcon]"></i>
              <span>{{item.menuName}}</span>
            </router-link>
          </template>
          <template v-else-if="item.menuSubLink">
            <a @click="isToggle(item.menuName,item.defaultActive)">
              <i :class="['fzicon',item.menuIcon]"></i>
              <span>{{item.menuName}}</span>
              <i class="trangle" :class="[config.iconfont, (item.menuName === toggleName) || item.defaultActive? config.icon_expand: config.icon_collapse]">
              </i>
            </a>
            <transition name="sliderToggle" mode="out-in">
              <ul class="tree-menu" v-show="item.menuName === toggleName || item.defaultActive">
                <li v-for="subitem in item.menuSubLink" :key="subitem.menuIcon" :class="currentUrl === subitem.menuUrl ? 'active':''">
                  <router-link :to="subitem.menuUrl">
                    <i :class="subitem.menuIcon"></i>
                    <span>{{subitem.menuName}}</span>
                  </router-link>
                </li>
              </ul>
            </transition>
          </template>
        </li>
      </template>
    </ul>


       
       
    </div>
</template>
<script>
import { MENULIST } from '../js/menuList'; // 
export default {
  name: "nav_aside",
  data() {
    return {
      menulist: MENULIST, // 
      currentUrl: '', // url
      toggleName: '',  // 
      config: {
        'iconfont': 'fzicon', // iconfont
        'icon_collapse': 'fz-ad-jiantou', // 
        'icon_expand': 'fz-ad-jiantou1' // 
      }
    };
  },
  created(){
    // console.log(this.$route);
  },
  methods: {
    changeli: function(ind, item) {
      // showfalse,v-if,
      this.headerData.forEach(i => {
        // headerData[i]showshowheaderData[i]false
        if (i.show !== this.headerData[ind].show) {
          i.show = false;
        }
      });
      // (truefalse)
      item.show = !item.show;
      console.log(item.name);
    },
    doThis: function(index) {
      console.log(index);
      if(this.headerData.list){
        this.headerData.list[index].show = true;
      }
    }
  },
  props: ['toggle', 'padMode'], // props,
  watch: {
    '$route'() {
      this.currentUrl = this.$route.fullPath; // 
    }

  },
  methods: {
    isToggle(name, defaultActive) {
      this.clearDefaultActive(); // ,
      defaultActive ? false : name !== this.toggleName ? this.toggleName = name : this.toggleName = ''; // 
    },
    clearDefaultActive() {
      this.menulist.forEach(item => {
        this.$delete(item, 'defaultActive')
      })
    }
  },
  created: function () {
    this.currentUrl = this.$route.fullPath;
    this.$nextTick(() => {
      this.menulist.forEach((item, index) => { // ,url(url)
        if (!item.menuSubLink && item.menuUrl) {
          this.currentUrl === item.menuUrl ? this.$set(item, 'defaultActive', true) : '';
        } else {
          if (item.menuSubLink) {
            item.menuSubLink.forEach((subitem, index) => {
              this.currentUrl === subitem.menuUrl ? this.$set(item, 'defaultActive', true) : '';
            })
          }
        }
      })
    })
  },
  mounted: function () {
  }
};
</script>
<style lang="less" scoped>
@base_color: #409EFF;
.el-menu-item-group__title {
  padding:0px;
}

/*  */
// 
.sliderToggle-enter-active,
.sliderToggle-leave-active {
  transition: all 0.5s linear;
  height: 100%;
  height: auto;
  overflow: hidden;
}

.sliderToggle-enter,
.sliderToggle-leave-to {
  overflow: hidden;
  padding-top: 0;
  padding-bottom: 0;
  height: 0;
  opacity: 0;
}
.header {
  width: 100%;
  background-color: #151515;
  & > ul {
    width: 100%;
    @include: clearfix;
     li {
      width: 100%;
      cursor: pointer; // float: left;
      line-height: 36px;
      &:hover {
        background-color: #262626;
      }
      & > a {
        padding-left: 10px;
        color: rgba(255, 255, 255, 0.7);
        font-size: 14px;
        display: block;
        height: 36px;
        line-height: 36px;
        border: 2px solid transparent;
      }
      &:hover {
        background-color: #262626;
        & > a {
          color: #ffffff;
        }
      }
      & > ul {
        width: 100%;
        // background: #463b3b;
        padding-left: 22px;
        
      }
    }
    .router-link-active {
      background-color: #262626;
      color: #ffffff;
      border-left: 3px solid @base_color;
    }
    .active {
      background-color: #262626;
      color: #ffffff;
      // border-left: 3px solid @base_color;
      a.router-link-active {
        background-color: #262626;
        color: #ffffff;
      // border-left: 3px solid @base_color;
      }
    }

  }
}
</style>




