<template>
  <div class="form_wrapper" @click.stop="keep_modal">
            <div class="form_title">
              FAQ
            </div>
            <div class="form_content">
              <!-- select -->
              <div class="selectBox">              
                <div class="selectLabel">
                  <p class="require"></p>
                </div>
                <div class="selectValue">
                <el-select v-model="value0" placeholder="">
                  <el-option
                    v-for="item in el.options0"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                  </el-select>
                  <el-select v-model="value1" placeholder="">
                    <el-option
                      v-for="item in el.options1"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-model="value2" placeholder="">
                    <el-option
                      v-for="item in el.options2"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-model="value2" placeholder="">
                    <el-option
                      v-for="item in el.options2"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </div>
              </div>
              <!--  -->
              <div class="inputBox">
                <div class="inputTitle">
                  <p class="require"></p>
                </div>
                <div class="inputValue yo-el-input">
                  <el-input :span='6' placeholder=""></el-input>
                </div>
                <div class="alike_box" v-show="search_show">
                  <h4>FAQ</h4>
                  <p v-for="n in 8" class="alike_q" @click="confirm_modal=true" :key="n"><span>[] </span></p>
                  <div class="modal_q" v-show="confirm_modal">
                    <h4>FAQ</h4>
                    <p @click="confirm_modal=false">
                      <button></button>                
                      <button></button>
                    </p>
                  </div>
                </div>                
              </div>
              <!--  -->
              <div class="edit">
                <div class="editTitle">
                  <p class="require"></p>
                  <edit_form class="ql_content"></edit_form>
                  <p class="addOther" @click="add_show = !add_show"> + </p>
                  <template v-if="add_show">
                    <p class="require"></p>
                    <edit_form class="ql_content"></edit_form>          
                  </template>
                </div>
              </div>
              <!--  -->
              <div class="recommend yo-el-checkbox">
                <h4>({{checkList.length}})</h4>
                <el-checkbox-group v-model="checkList" @change="end_value">
                  <template v-for="(item,index) in recommend_list">
                    <el-checkbox :label="item.content" :key="index" :title="item.content"></el-checkbox>                      
                  </template>
                </el-checkbox-group>
              </div>
            </div>
            <div class="form_btn">
                <div class="yo-btn">
                    <el-button round @click.stop="close_modal"></el-button>
                    <el-button type="primary" round></el-button>
                </div>
            </div>
          </div>
</template>
<script>
import edit_form from "../edit_form.vue";
export default {
  name: "history_form",
  data() {
    return {
      checkList: [],
      recommend_list: [
        {
          id: 0,
          content: ""
        },
        {
          id: 1,
          content: ""
        },
        {
          id: 2,
          content: ""
        },
        {
          id: 3,
          content: ""
        },
        {
          id: 4,
          content: ""
        },
        {
          id: 5,
          content: ""
        },
        {
          id: 6,
          content: ""
        }
      ],
      confirm_modal: false,
      search_show: true,
      add_show: false,
      value0: "",
      value1: "",
      value2: "",
      el: {
        options0: [
          {
            value: "1",
            label: ""
          },
          {
            value: "2",
            label: ""
          },
          {
            value: "3",
            label: ""
          },
          {
            value: "4",
            label: ""
          }
        ],
        options1: [
          {
            value: "1.1",
            label: "0"
          },
          {
            value: "1.2",
            label: "1"
          },
          {
            value: "1.3",
            label: "2"
          },
          {
            value: "1.4",
            label: "3"
          }
        ],
        options2: [
          {
            value: "1.1.1",
            label: "1"
          },
          {
            value: "1.1.2",
            label: "2"
          },
          {
            value: "1.1.3",
            label: "3"
          },
          {
            value: "1.1.4",
            label: "4"
          }
        ]
      },
      form_content: [
        {
          title: "FAQ",
          content: "[]",
          word: ""
        },
        {
          title: "",
          content: "",
          word: ""
        },
        {
          title: "",
          content: "",
          word: ""
        },
        {
          title: "",
          content: "",
          word: ""
        }
      ]
    };
  },
  props:[
    // 'showModal'
  ],
  mounted () {
    // console.log(showModal);
  },
  components: {
    edit_form
  },
  methods: {
    close_modal() {
      this.$parent.show_fade = false;
    },
    keep_modal() {
      this.$parent.show_fade = true;
    },
    setValue() {},
    end_value() {
      console.log(this.checkList);
    }
  }
};
</script>
<style lang="less" scoped>
.text_es() {
  overflow: hidden;
  text-overflow: ellipsis;
  /* */
  display: -webkit-box;
  /* */
  -webkit-line-clamp: 1;
  /*  */
  -webkit-box-orient: vertical;
}
.require {
  padding-left: 14px;
  position: relative;
  &::before {
    position: absolute;
    left: 0px;
    top: 7px;
    content: "";
    display: block;
    height: 4px;
    width: 4px;
    background-color: #ff0000;
  }
}
.addOther {
  display: inline-block;
  color: #409eff;
  padding: 12px 0px;
  cursor: pointer;
}
.form_wrapper {
  text-align: left;
  display: flex;
  flex-direction: column;
  /*  */
  .form_title {
    line-height: 40px;
    color: #333333;
    font-size: 14px;
    padding-left: 20px;
    height: 40px;
    border-bottom: 1px solid #f5f5f5;
  }
  /*  */
  .form_content {
    flex: 1;
    padding: 24px;
    overflow: auto;
    .selectBox {
      margin-bottom: 20px;
      .el-select {
        padding: 10px 5px;
        border-radius: 53px;
        overflow: hidden;
        font-size: 14px;
        color: #333333;
      }
      .selectValue {
        display: flex;
        width: 100%;
        height: 100%;
      }
      .selectLabel {
        width: 100%;
      }
    }
    .inputValue {
      margin-top: 10px;
      margin-bottom: 20px;
      height: 36px;
      input {
        height: 100%;
      }
    }
    .alike_box {
      margin-bottom: 20px;
      position: relative;
      h4 {
        color: #b2b2b2;
      }
      .alike_q {
        padding: 10px 0px 0px 0px;
        color: #409eff;
        max-width: 70%;
        .text_es();
        cursor: pointer;
      }
      .modal_q {
        height: 68px;
        width: 180px;
        border: 1px solid #dcdfe6;
        border-radius: 0px 12px 0px 0px;
        position: absolute;
        right: 0px;
        top: 0px;
        text-align: center;
        padding: 10px;
        background-color: #fefefe;
        box-shadow: 3px 4px #cccccc;
        h4 {
          padding-bottom: 8px;
        }
        button {
          height: 22px;
          width: 48px;
          font-size: 12px;
          display: inline-block;
          // line-height: 1;
          white-space: nowrap;
          cursor: pointer;
          background: #fff;
          border: 1px solid #dcdfe6;
          color: #606266;
          -webkit-appearance: none;
          text-align: center;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          outline: 0;
          margin: 0;
          -webkit-transition: 0.1s;
          transition: 0.1s;
          font-weight: 500;
          line-height: 22px;
          border-radius: 14px;
          margin: 0px 8px;
          &:hover {
            color: #409eff;
            border-color: #c6e2ff;
            background-color: #ecf5ff;
          }
          &:nth-last-child(1) {
            background-color: #409eff;
            color: #ffffff;
            &:hover {
              background: #66b1ff;
              border-color: #66b1ff;
              color: #fff;
            }
          }
        }
      }
    }
    .ql_content {
      margin-top: 10px;
      // margin-bottom:20px;
    }
    .recommend {
      h4 {
        padding-top: 26px;
        padding-bottom: 20px;
      }
      .el-checkbox {
        padding-bottom: 14px;
      }
    }
  }
  /*  */
  .form_btn {
    text-align: right;
    padding: 24px;
    .yo-btn {
      .el-button {
        padding: 11px 25px;
      }
      .el-button + .el-button {
        margin-left: 20px;
      }
    }
  }
}
</style>




