<template>
  <div class="form_wrapper" @click.stop="keep_modal" ref="editer_child">
            <div class="form_title">
              FAQ
            </div>
            <div class="form_content">
              <!-- select -->
              <div class="selectBox">              
                <div class="selectLabel">
                  <p class="require"></p>
                </div>
                <div class="selectValue">
                  <template v-for="(item,key,index) in el">
                    <el-select v-model="value[index]" placeholder="" :key="index" @change="changeData(value,index)">
                      <el-option label="" value="">                        
                      </el-option>
                      <el-option
                        v-for="item_1 in item"
                        :key="item_1.value"
                        :label="item_1.label"
                        :value="item_1.value">
                      </el-option>
                    </el-select>
                  </template>
                </div>
              </div>
              <!--  -->
              <div class="inputBox">
                <div class="inputTitle">
                  <p class="require"></p>
                </div>
                <div class="inputValue yo-el-input">
                  <el-input :span='6' placeholder="" v-model="similarAsk"  v-on:input="getsimilarfaq()"></el-input>
                </div>
                 <div class="alike_box" v-if="search_show">
                  <h4>FAQ</h4>
                  <p v-for="item in similarFaq" class="alike_q" :key="item.id"><span>{{item.class}}</span>{{item.question}}</p>
                  <div class="modal_q" v-if="confirm_modal">
                    <h4>FAQ</h4>
                    <p @click="confirm_modal=false">
                      <button></button>                
                      <button @click.stop="getId(small_modal_id)"></button>
                    </p>
                  </div>
                </div>               
              </div>
              <!--  -->
              <div class="edit">
                <div class="editTitle">
                  <p class="require"></p>
                  <edit_form class="ql_content" :answer="answerToChild" @answer="getChild"></edit_form>
                  <!-- <p class="addOther" @click="add_show = !add_show"> + </p> -->
                  <template v-if="add_show">
                    <p class="require"></p>
                  <edit_form class="ql_content" ></edit_form>
                  </template>
                </div>
              </div>
              <div>
                <img_uploader @toImg="showImgFromChild" :imgurl= "imgurl"></img_uploader>  
              </div>
              <!--  -->
              <div class="recommend yo-el-checkbox">
                <h4>({{checkList.length}})</h4>
                <el-checkbox-group v-model="checkList" >
                  <template v-for="(item,index) in getsimilarask">
                    <el-checkbox :label="item.question" :key="index" :id="item.id" @change="end_value(item.id)" :checked="index===0?true:false"></el-checkbox>
                  </template> 
                </el-checkbox-group>
              </div>
            </div>

            <div class="form_btn">
                <div class="yo-btn">
                    <el-button round @click.stop="close_modal"></el-button>
                    <el-button type="primary" round @click.stop="saveEditFaq()" ></el-button>
                </div>
            </div>
          </div>
</template>
<script>
import edit_form from "../edit_form";
import img_uploader from "../img_uploader";
export default {
  name: "find_form",
  data() {
    return {
      ac_getId: true,
      all_data: {},
      getFather: {
        id: this.getData1.id,
        title: this.getData1.title
      },
      getsimilarask:this.getData1.all_similar,
      checkList: [],
      el: {
        option0: [
          {
            value: "",
            label: ""
          }
        ],
        option1: [
          {
            value: "",
            label: ""
          }
        ]
      },
      small_modal_id: 0,
      confirm_modal: false,
      search_show: true,
      add_show: false,
      value: [],
      title: this.getData1.title,
      el: {},
      // el: this.getData1.select_data,
      form_push: {
        question: this.getData1.title,
        question_id: this.getData1.id,
        answer: "",
        img: "",
        class: [],
        similar_data: [],
        uid:  this.$cm.uid
      },
      form_content: [
        {
          title: "FAQ",
          content: "[]",
          word: ""
        }
      ],
      similarAsk: "",
      similarFaq: [],
      answerToChild: "",
      imgurl: "",
      slectedSimilarAsk:[]
    };
  },
  props: {
    getData1: {
      type: Object
    }
  },
  created() {
    this.similarAsk = this.getData1.title;
    this.getsimilarfaq();
    // console.log(this.getData1.title, 888555);
  },
  mounted() {
    // console.log(this.getData1);
    this.$cm.ajax(
      "POST",
      "Faq.faq_getchooseclass",
      {},
      res => {
        this.el = res.data.result.data;
      },
      this
    );
    //faq
    console.log(this.getData1);
  },
  components: {
    edit_form,
    img_uploader
  },
  methods: {
    getChild(data){
      console.log(data,"");
      this.form_push.answer = data;
    },
    initPushData(){
      this.similar_data=this.getData1.all_similar[0];
    },
    //imgURL
    showImgFromChild(data) {
      this.form_push.img = data;
      console.log(data,"");
    },
    /* select */
    changeData(val, i) {
      var param = val[i];
      if (i > 0 && val[i] == "") {
        param = val[i - 1];
      }
      console.log(param);
      this.form_push.class = param;
      this.$cm.ajax(
        "POST",
        "Faq.faq_getchooseclass",
        { id: param },
        res => {
          this.el = res.data.result.data;
        },
        this
      );
      this.form_push.class = this.value;
      console.log(this.form_push.class,"select");
    },
    show_small(id) {
      this.confirm_modal = true;
      this.small_modal_id = id;
      console.log(this.small_modal_id);
    },
    getId(id) {
      // this.$parent.show_fade = false;
      console.log(id);
      //faq
      this.$cm.ajax(
        "POST",
        "Faq.faq_getquestioninfo",
        { id: id },
        res => {
          for (var i in res.data.result.choose) {
            this.value.push(res.data.result.choose[i]); //
          }
          this.similarAsk = res.data.result.question; //
          this.answerToChild = res.data.result.answer; //
          this.imgurl = res.data.result.answer_img; //
          console.log(this.answerToChild);
        },
        this
      );
    },
    // saveNewFaq(question,answer,img,getClass,similar_data){
    //   this.$cm.ajax("POST","Faq.faq_savenewfaq",{question:question,answer:answer,img:img,class:getClass,similar_data:similar_data},(res)=>{
    //       console.log(res);
            
    //   },this);      

    // },
    //FAQ
    saveEditFaq(id, question, answer, img, getClass) {
      this.end_value();
      console.log(this.form_push);
      this.$cm.ajax(
        "POST",
        "Faq.faq_savenewclusterfaq",
        this.form_push,
        res => {
          console.log(res);
          this.close_modal();
        },
        this
      );
    },
    close_modal() {
      this.$parent.show_fade = false;
    },
    keep_modal() {
      this.$parent.show_fade = true;
    },
    setValue() {},
    end_value() {
      console.log(this.getsimilarask);
      this.slectedSimilarAsk = [];
      for (var i = 0; i < this.checkList.length; i++) {
        this.slectedSimilarAsk.push({
          question: this.checkList[i]
        });
      }
      for (var i = 0; i < this.checkList.length; i++) {
        for (var j = 0; j < this.getsimilarask.length; j++) {
          if (this.checkList[i] == this.getsimilarask[j].question) {
            this.slectedSimilarAsk[i].id = this.getsimilarask[j].id;
          }
        }
      }
      this.form_push.similar_data = this.slectedSimilarAsk;
      console.log(this.slectedSimilarAsk);
    },
    //faq
    getsimilarfaq() {
      this.search_show = true;
      this.$cm.ajax(
        "POST",
        "Faq.faq_getsimilarfaq",
        { ask: this.similarAsk },
        res => {
          this.similarFaq = res.data.result.data;
        },
        this
      );
    }
  }
};
</script>
<style lang="less" scoped>
.text_es() {
  overflow: hidden;
  text-overflow: ellipsis;
  /* */
  display: -webkit-box;
  /* */
  -webkit-line-clamp: 1;
  /*  */
  -webkit-box-orient: vertical;
}
.require {
  padding-left: 14px;
  position: relative;
  &::before {
    position: absolute;
    left: 0px;
    top: 7px;
    content: "";
    display: block;
    height: 4px;
    width: 4px;
    background-color: #ff0000;
  }
}
.addOther {
  display: inline-block;
  color: #409eff;
  padding: 12px 0px;
  cursor: pointer;
}
.form_wrapper {
  text-align: left;
  display: flex;
  flex-direction: column;
  /*  */
  .form_title {
    line-height: 40px;
    color: #333333;
    font-size: 14px;
    padding-left: 20px;
    height: 40px;
    border-bottom: 1px solid #f5f5f5;
  }
  /*  */
  .form_content {
    flex: 1;
    padding: 24px;
    overflow: auto;
    .selectBox {
      margin-bottom: 20px;
      .el-select {
        padding: 10px 5px;
        border-radius: 53px;
        overflow: hidden;
        font-size: 14px;
        color: #333333;
      }
      .selectValue {
        display: flex;
        width: 100%;
        height: 100%;
      }
      .selectLabel {
        width: 100%;
      }
    }
    .inputValue {
      margin-top: 10px;
      margin-bottom: 20px;
      height: 36px;
      input {
        height: 100%;
      }
    }
    .alike_box {
      margin-bottom: 20px;
      position: relative;
      h4 {
        color: #b2b2b2;
      }
      .alike_q {
        padding: 10px 0px 0px 0px;
        color: #409eff;
        max-width: 70%;
        .text_es();
        cursor: pointer;
      }
      .modal_q {
        height: 68px;
        width: 180px;
        border: 1px solid #dcdfe6;
        border-radius: 0px 12px 0px 0px;
        position: absolute;
        right: 0px;
        top: 0px;
        text-align: center;
        padding: 10px;
        background-color: #fefefe;
        box-shadow: 3px 4px #cccccc;
        h4 {
          padding-bottom: 8px;
        }
        button {
          height: 22px;
          width: 48px;
          font-size: 12px;
          display: inline-block;
          // line-height: 1;
          white-space: nowrap;
          cursor: pointer;
          background: #fff;
          border: 1px solid #dcdfe6;
          color: #606266;
          -webkit-appearance: none;
          text-align: center;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          outline: 0;
          margin: 0;
          -webkit-transition: 0.1s;
          transition: 0.1s;
          font-weight: 500;
          line-height: 22px;
          border-radius: 14px;
          margin: 0px 8px;
          &:hover {
            color: #409eff;
            border-color: #c6e2ff;
            background-color: #ecf5ff;
          }
          &:nth-last-child(1) {
            background-color: #409eff;
            color: #ffffff;
            &:hover {
              background: #66b1ff;
              border-color: #66b1ff;
              color: #fff;
            }
          }
        }
      }
    }
    .ql_content {
      margin-top: 10px;
      // margin-bottom:20px;
    }
    .recommend {
      h4 {
        padding-top: 26px;
        padding-bottom: 20px;
      }
      .el-checkbox {
        padding-bottom: 14px;
      }
    }
  }
  /*  */
  .form_btn {
    text-align: right;
    padding: 24px;
    .yo-btn {
      .el-button {
        padding: 11px 25px;
      }
      .el-button + .el-button {
        margin-left: 20px;
      }
    }
  }
}
</style>




