<template>
  <div id="app" @click="init_modal">
    <template v-if="login_show">
      <login ref="login"></login>
    </template>
    <template v-else>
      <div class="top">
        <first></first>
      </div>
      <div class="main">
        <div class="aside">
          <second></second>
        </div>
        <div class="content">
          <router-view class="view"></router-view>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
//
import first from "./component/blocks/header.vue";
import second from "./component/blocks/nav.vue";
import home from "./component/pages/home.vue";
import login from "./component/pages/login.vue";
import edit_form from "./component/blocks/edit_form.vue";
//data
export default {
  data() {
    return {
      msg: "yobot",
      login_show: true
    };
  },
  watch:{
    $router(to,from){
      console.log(to);
    }
  },
  created() {
    var cookie = this.$cm.getCookie();
    // console.log(cookie, "jiazahuoqu");
    if (!cookie.uid) {
      this.login_show = true;
    } else {
      this.login_show = false;
      this.$cm.uid = cookie.uid;
    }
    // console.log(this.$cm.getCookie(),666666666666666);
    // let cookie = this.$cm.getCookie();
  },
  mounted() {
    this.$cm.nocopy();
  },
  components: {
    first,
    second,
    home,
    edit_form,
    login
  },
  methods: {
    init_modal() {}
  }
};
</script>

<style lang="less">
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-weight: normal;
  letter-spacing: 0.4;
}
@color: rgb(79,180,255);
h1,
h2,
h3,
h4,
h5 {
  font-size: 14px;
  font-weight: normal;
}
html {
  font-size: 16px;
  width: 100%;
  height: 100%;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "", Arial, sans-serif;
}
body {
  width: 100%;
  height: 100%;
  font-size: 14px;
}

/* trf7f7f7 */
.el-table--striped .el-table__body tr.el-table__row--striped.current-row td,
.el-table__body tr.current-row > td,
.el-table__body tr.hover-row.current-row > td,
.el-table__body tr.hover-row.el-table__row--striped.current-row > td,
.el-table__body tr.hover-row.el-table__row--striped > td,
.el-table__body tr.hover-row > td {
  background-color: #f7f7f7;
}
#app tr {
  &:hover {
    background-color: #f7f7f7;
  }
}

/*  */
#app .all-words {
  .el-tag {
    height: 30px;
  }
  .input-new-tag {
    border: none;
    display: inline-block;
    vertical-align: top;
    width: 90px;
    height: 30px;
    input {
      display: inline-block;
      height: 30px;
    }
  }
  .el-tag__close.el-icon-close {
    border-radius: 50%;
    text-align: center;
    position: absolute;
    cursor: pointer;
    font-size: 12px;
    height: 16px;
    width: 16px;
    line-height: 16px;
    vertical-align: middle;
    top: 7px;
    right: 5px;
    color: #333;
  }
}

/*  */
.pagination_bottom {
  text-align: right;
}

/* check */
.el-checkbox__inner {
  border-radius: 50%;
  overflow: hidden;
}

/*  */
.form_btn {
  text-align: right;
  padding: 24px;
  .yo-btn {
    .el-button {
      padding: 11px 25px;
    }
    .el-button + .el-button {
      margin-left: 20px;
    }
  }
}

/* radio */
.el-radio-group {
  width: 100%;
  .el-radio__input {
    float: right;
  }
  .el-radio__label {
    padding-left: 0px;
    float: left;
    max-width: 90%;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
.el-radio + .el-radio,
.el-radio.alike_radio {
  margin-left: 0px;
  display: block;
}
// .el-radio, .el-radio__inner, .el-radio__input

/*  */
input::-webkit-input-placeholder {
  color: rgb(192, 196, 204);
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: rgb(192, 196, 204);
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: rgb(192, 196, 204);
}
input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: rgb(192, 196, 204);
}

/*  */
.searchBox {
  .el-input__icon {
    // margin-top:-6px;
    line-height: 26px;
    margin-right: 4px;
    color: #333333;
    font-size: 20px;
  }
}

/* table */
#app {
  .filter_box {
    .el-select .el-input .el-select__caret {
      width: 12px;
      height: 12px;
    }
    .el-input--suffix .el-input__inner {
      height: 12px;
    }
    .el-input__inner {
      border: none;
      width: 12px;
      // visibility: hidden;
      display: none;
    }
    .el-input__suffix {
      right: -50px;
      top: -11px;
      i {
        color: #333333;
      }
    }
  }
}

/*  */
#app .mianbaoxie {
  .el-breadcrumb__inner,
  .el-breadcrumb__inner a {
    color: #999999;
    font-weight: normal;
  }
}

/* tree */
#app {
  .el-tree-node.is-focusable {
    padding: 4px 0px;
  }
  .el-tree-node.el-tree {
    color: #4c4c4c;
  }
}
.is-current > .el-tree-node__content {
  color: @color;
}

/* el */
.login-input {
  .el-button--primary {
    width: 166px;
    background-color: @color;
    border-color: @color;
    border-radius: 20px;
    &:hover {
      // background: #24C79E;
      // border-color: #24C79E;
      color: #fff;
    }
  }
}

/*  */
.clearfix {
  &::before,
  &::after {
    content: "";
    display: table;
  }
  &::after {
    clear: both;
  }
}
.el-checkbox {
  color: #b2b2b2;
}

/*  */
.yo-submit-btn,
.yo-cancle-btn {
  height: 36px;
  width: 80px;
  margin: 14px 10px;
  border: 1px solid @color;
  font-size: 14px;
  border-radius: 18px;
  text-align: center;
  cursor: pointer;
}
.yo-submit-btn {
  color: @color;
}
.yo-cancle-btn {
  color: #ffffff;
  background-color: @color;
}

/* modal */
.alike_box .modal_q h4 {
  font-size: 12px;
}

/*  */

.fade_form {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
  transition: background-color 0.3s ease-out;
  text-align: right;
  .form_wrapper {
    position: absolute;
    overflow-y: hidden;
    box-sizing: border-box;
    width: 33%;
    height: 100%;
    font-size: 14px;
    background-color: #fff;
    right: 0;
    top: 0;
    transition: all 0.3s ease-out;
  }
}

body {
  user: none; /* CSS3 */
}

#app {
  overflow-y: hidden;
  overflow-x: auto;
  height: 100%;
  min-width: 1366px;
  display: flex;
  flex-direction: column;

  /*  */
  .yo-el-checkbox {
    color: #b2b2b2;
    .el-checkbox {
      width: 100%;
      .el-checkbox__input {
        float: right;
      }
      .el-checkbox__label {
        float: left;
        padding-left: 0px;
        overflow: hidden;
        max-width: 90%;
        text-overflow: ellipsis;
      }
    }
    .el-checkbox + .el-checkbox {
      margin-left: 0px;
    }
  }

  /*  */
  .el-input {
    height: 100%;
    & > input {
      height: 100%;
    }
  }

  .el-date-editor.el-input,
  .el-date-editor.el-input__inner {
    width: 230px;
  }
  /*  */
  .el-input--suffix .el-input__inner {
    height: 36px;
  }
  .searchBox .el-input--suffix .el-input__inner {
    height: 28px;
  }

  /*  */
  .el-date-editor .el-range-input {
    width: 35%;
  }
  .el-input__inner {
    border-radius: 24px;
  }
  .top {
    height:53px;
    background-color: #ffffff;
  }
  .main {
    // flex: 1;
    height:~"calc(100% - 53px)";
    display: flex;
    .aside {
      width: 11%;
      height: 100%;
      min-width: 160px;
      background-color: #151515;
      overflow-y: auto;
      overflow-x: hidden;
    }
    .content {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: left;
      flex: 1;
      height: 100%;
      width: 100%;
      background-color: #f8f9fb;
      overflow-x: hidden;
      overflow-y: hidden;
      .view {
        width: 100%;
        height: 100%;
        overflow-x: hidden;
      }
    }
  }
}
</style>



