import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

/*  */
const state = {
    count: 0
}
const handle = {
    add(state){
        state++;
    }
}
var vuexStore = new Vuex.Store({
    state,
    handle
});

//  export default 
console.log(vuexStore);
export default vuexStore;
