<template>
  <div class="form_wrapper" @click="keep_modal($event)" ref="editer_child">
    <div class="form_title">
      FAQ
    </div>
    <div class="form_content">
      <!-- select -->
      <div class="selectBox">              
        <div class="selectLabel">
          <p class="require"></p>
        </div>
        <div class="selectValue">
          <template v-for="(item,key,index) in getchooseclass">
            <el-select v-model="value[index]" placeholder="" :key="index" @change="changeData(value,index)">
              <el-option
                v-for="item_1 in item"
                :key="item_1.value"
                :label="item_1.label"
                :value="item_1.value">
              </el-option>
            </el-select>
          </template>
        </div>
      </div>
      <!--  -->
      <div class="inputBox">
        <div class="inputTitle">
          <p class="require"></p>
        </div>
        <div class="inputValue yo-el-input">
          <el-input :span='6' placeholder="" v-model="similarAsk"  v-on:input="getsimilarfaq()"></el-input>
        </div>
        <div class="alike_box" v-if="search_show">
          <h4>FAQ</h4>
          <p v-for="(item,index) in similarFaq" class="alike_q" @click="confirm_modal=true" :key="index"><span>{{item.question}}</span></p>
          <div class="modal_q" v-if="confirm_modal">
            <h4>FAQ</h4>
            <p @click="confirm_modal=false">
              <button></button>                
              <button></button>
            </p>
          </div>
        </div>                
      </div>
      <!--  -->
      <div class="edit">
        <div class="editTitle">
          <p class="require"></p>
          <edit_form class="ql_content" :answer="answerToChild" v-on:answer="showAnswerFromChild"></edit_form>
        </div>
      </div>
      <!--  -->
      <div>
        <img_uploader v-on:toImg="showImgFromChild" :imgurl= "imgurl"></img_uploader>  
      </div>
    </div>
    <div class="form_btn">
        <div class="yo-btn">
            <el-button round @click.stop="close_modal"></el-button>
            <el-button type="primary" round @click.stop="saveEditFaq(slectedId,similarAsk,answerToChild,imgurl,value)"></el-button>
        </div>
    </div>
  </div>
</template>
<script>
import edit_form from "../edit_form.vue";
import img_uploader from "../img_uploader.vue";
export default {
  name: "history_form",
  data() {
    return {
      checkList: [],
      confirm_modal: false,
      search_show: false,
      add_show: false,
      value: [],
      title: this.getData1.title,
      el: this.getData1.select_data,
      getchooseclass: {},
      form_push: {
        question: this.similarAsk,
        question_id: this.getData1.id,
        answer: "",
        img: "",
        class: "",
        similar_data: "",
        uid: ""
      },
      similarAsk: "",
      similarFaq: [],
      answerToChild: "",
      imgurl: []
    };
  },
  props: ["slectedId", "getData1"],
  mounted() {
    console.log(this.slectedId, this.getData1);
    this.init_select_by_id();
    this.get_value_by_id();
  },
  components: {
    edit_form,
    img_uploader
  },
  methods: {
    /*  */
    init_select_by_id(id) {
      /* select */
      this.$cm.ajax(
        "GET",
        "Faq.faq_getchooseclass",
        { id: id },
        res => {
          this.getchooseclass = res.data.result.data;
          console.log(this.getchooseclass);
        },
        this
      );
    },
    get_value_by_id() {
      //faq
      this.$cm.ajax(
        "POST",
        "Faq.faq_getquestioninfo",
        { id: this.slectedId },
        res => {
          if (res.data.result.choose.length == 0) {
            this.value = [];
          } else {
            for (var i in res.data.result.choose) {
              this.value.push(res.data.result.choose[i]); //
            }
          }
          console.log(this.value);
          this.init_select_by_id(this.value[this.value.length - 1]);
          this.similarAsk = res.data.result.question; //
          this.answerToChild = res.data.result.answer; //
          if (res.data.result.answer_img.length == 0) {
            this.imgurl = [];
          } else {
            this.imgurl = res.data.result.answer_img; //
          }
          console.log(this.imgurl);
        },
        this
      );
    },
    /* select */
    changeData(val, i) {
      var param = val[i];
      if (i > 0 && val[i] == "") {
        param = val[i - 1];
      } else {
        var temp = this.value;
        for (var j = 1; j < temp.length - i; j++) {
          this.value[i + j] = "";
        }
        console.log(this.value);
      }
      // console.log(param);
      this.form_push.class = param;
      this.$cm.ajax(
        "POST",
        "Faq.faq_getchooseclass",
        { id: param },
        res => {
          this.getchooseclass = res.data.result.data;
          this.value = res.data.result.choose;
          console.log(this.value);
        },
        this
      );
    },
    //imgURL
    showImgFromChild(data) {
      this.imgurl = data;
    },
    showAnswerFromChild(data) {
      this.answerToChild = data;
    },
    //FAQ
    saveEditFaq(id, question, answer, img, getClass) {
      this.$cm.ajax(
        "POST",
        "Faq.faq_saveeditfaq",
        {
          id: id,
          question: question,
          answer: answer,
          img: img.length==0?"":img,
          class: getClass
        },
        res => {
          console.log(res.data.code);
          if (res.data.code == "200") {
            this.$parent.show_fade = false;
            this.$cm.ajax(
              "GET",
              "Faq.faq_getquestion",
              "",
              res => {
                console.log(res);
                this.$parent.faq_ques = res.data.result.data;
                this.$parent.init_home_data(
                  this.$parent.curID,
                  this.$parent.page
                );
              },
              this
            );
          } else if (res.data.code == "300") {
            alert(res.data.message);
          }
        },
        this
      );
    },
    close_modal() {
      this.$parent.show_fade = false;
    },
    keep_modal(e) {
      console.log(e);
      e.stopPropagation();      
      // this.$parent.show_fade = true;
    },
    setValue() {},
    end_value() {
      // console.log(this.checkList);
    },
    //faq
    getsimilarfaq() {
      this.search_show = true;
      this.$cm.ajax(
        "POST",
        "Faq.faq_getsimilarfaq",
        { ask: this.similarAsk },
        res => {
          this.similarFaq = res.data.result.data;
        },
        this
      );
    }
  }
};
</script>
<style lang="less" scoped>
.text_es() {
  overflow: hidden;
  text-overflow: ellipsis;
  /* */
  display: -webkit-box;
  /* */
  -webkit-line-clamp: 1;
  /*  */
  -webkit-box-orient: vertical;
}
.require {
  padding-left: 14px;
  position: relative;
  &::before {
    position: absolute;
    left: 0px;
    top: 7px;
    content: "";
    display: block;
    height: 4px;
    width: 4px;
    background-color: #ff0000;
  }
}
.addOther {
  display: inline-block;
  color: #409eff;
  padding: 12px 0px;
  cursor: pointer;
}
.form_wrapper {
  text-align: left;
  display: flex;
  flex-direction: column;
  /*  */
  .form_title {
    line-height: 40px;
    color: #333333;
    font-size: 14px;
    padding-left: 20px;
    height: 40px;
    border-bottom: 1px solid #f5f5f5;
  }
  /*  */
  .form_content {
    flex: 1;
    padding: 24px;
    overflow: auto;
    .selectBox {
      margin-bottom: 20px;
      .el-select {
        padding: 10px 5px;
        border-radius: 53px;
        overflow: hidden;
        font-size: 14px;
        color: #333333;
      }
      .selectValue {
        display: flex;
        width: 100%;
        height: 100%;
      }
      .selectLabel {
        width: 100%;
      }
    }
    .inputValue {
      margin-top: 10px;
      margin-bottom: 20px;
      height: 36px;
      input {
        height: 100%;
      }
    }
    .alike_box {
      margin-bottom: 20px;
      position: relative;
      h4 {
        color: #b2b2b2;
      }
      .alike_q {
        padding: 10px 0px 0px 0px;
        color: #409eff;
        max-width: 70%;
        .text_es();
        cursor: pointer;
      }
      .modal_q {
        height: 68px;
        width: 180px;
        border: 1px solid #dcdfe6;
        border-radius: 0px 12px 0px 0px;
        position: absolute;
        right: 0px;
        top: 0px;
        text-align: center;
        padding: 10px;
        background-color: #fefefe;
        box-shadow: 3px 4px #cccccc;
        h4 {
          padding-bottom: 8px;
        }
        button {
          height: 22px;
          width: 48px;
          font-size: 12px;
          display: inline-block;
          // line-height: 1;
          white-space: nowrap;
          cursor: pointer;
          background: #fff;
          border: 1px solid #dcdfe6;
          color: #606266;
          -webkit-appearance: none;
          text-align: center;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          outline: 0;
          margin: 0;
          -webkit-transition: 0.1s;
          transition: 0.1s;
          font-weight: 500;
          line-height: 22px;
          border-radius: 14px;
          margin: 0px 8px;
          &:hover {
            color: #409eff;
            border-color: #c6e2ff;
            background-color: #ecf5ff;
          }
          &:nth-last-child(1) {
            background-color: #409eff;
            color: #ffffff;
            &:hover {
              background: #66b1ff;
              border-color: #66b1ff;
              color: #fff;
            }
          }
        }
      }
    }
    .ql_content {
      margin-top: 10px;
      // margin-bottom:20px;
    }
    .recommend {
      h4 {
        padding-top: 26px;
        padding-bottom: 20px;
      }
      .el-checkbox {
        padding-bottom: 14px;
      }
    }
  }
  /*  */
  .form_btn {
    text-align: right;
    padding: 24px;
    .yo-btn {
      .el-button {
        padding: 11px 25px;
      }
      .el-button + .el-button {
        margin-left: 20px;
      }
    }
  }
}
</style>




