<template>
  <div class="form_wrapper" @click.stop="show_modal" id="words-manager-form">
    <div class="form_title">
      
    </div>
    <div class="form_content">
      <ul>
        <li v-if="content.qid">
          <h4></h4>
          <div class="words-code">{{content.qid}}</div>
        </li>
        <li class="change-words">
          <div class="main-word">
            <h4 class="require"></h4>
            <div class="main-word-content">
              <el-input v-model="content.name" placeholder=""></el-input>
            </div>
          </div>
          <div class="word-weight">
            <h4 class="require"></h4>
            <div class="word-weight-content">
              <el-select v-model="content.weight">
                <el-option value="1">1</el-option>
                <el-option value="2">2</el-option>
                <el-option value="3">3</el-option>
              </el-select>
            </div>
          </div>
        </li>
        <li>
          <h4 class="require"></h4>
          <div class="all_words">
            <el-tag
              v-for="(tag,index) in content.other"
              :key="index"
              closable
              :disable-transitions="false"
              @close="handleClose(tag)">
              {{tag.content}}
            </el-tag>
            <div class="add_input">
              <el-input
                class="input-new-tag"
                v-if="inputVisible"
                v-model="inputValue"
                ref="saveTagInput"
                size="medium"
                @keyup.enter.native="handleInputConfirm"
                @blur="handleInputConfirm"
                placeholder=""
              >
              </el-input>
              <el-button
                v-else
                class="button-new-tag"
                @click="showInput"
              >+ 
              </el-button>              
            </div>
          </div>
        </li>
      </ul>
    </div>
    <!--  -->
    <div class="form_btn">
      <div class="yo-btn">
          <el-button round @click.stop="close_modal"></el-button>
          <el-button type="primary" round @click.stop="save_words(content.qid)"></el-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form_content: [],
      inputVisible: false,
      inputValue: "",
      content: {
        qid: "",
        name: "",
        weight: 1,
        other: []
      }
    };
  },
  props: ["getData"],
  created() {},
  mounted() {
    this.data_init();
  },
  methods: {
    data_init() {
      var flag = false;
      if (this.getData.qid) {
        // console.log(this.$cm);
        // this.content = this.$cm.cloneObj(this.getData);
        this.$cm.ajax(
          "POST",
          "Faq.faq_worddetial",
          { qid: this.getData.qid },
          res => {
            console.log(res);
            this.content = res.data.result;
            console.log(this.content);
          },
          this
        );
      } else {
        this.content = {
          qid: "",
          name: "",
          weight: 1,
          other: []
        };
      }

      console.log(this.content);
    },
    show_modal() {
      this.$parent.show_fade = true;
    },
    handleClose(tag) {
      this.content.other.splice(this.content.other.indexOf(tag), 1);
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },

    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue && this.content.other.indexOf(inputValue) === -1) {
        this.content.other.push({
          id: "",
          content: inputValue
        });
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    close_modal() {
      this.$parent.show_fade = false;
    },
    save_words(qid) {
      if (this.content.other.length == 0) {
        this.$alert("");
      } else {
        if (qid) {
          //qid
          // alert(5);
          var params = {
            qid: this.content.qid,
            name: this.content.name,
            word: this.content.other,
            weight: this.content.weight
          };
          console.log(params);
          this.$cm.ajax(
            "POST",
            "Faq.faq_editword",
            params,
            res => {
              this.$message({
                type: "success",
                message: ""
              });
              this.$parent.init_table_data(this.$parent.params);
            },
            this
          );
        } else {
          // alert(6)
          //qid
          var params = {
            name: this.content.name,
            word: this.content.other,
            weight: this.content.weight
          };
          console.log(params);
          this.$cm.ajax(
            "POST",
            "Faq.faq_addword",
            params,
            res => {
              this.$message({
                type: "success",
                message: ""
              });
              this.$parent.init_table_data(this.$parent.params);
            },
            this
          );
        }
        this.close_modal();
      }
    }
  }
};
</script>
<style lang="less">
@color: #4fb4ff;
.require {
  padding-left: 14px;
  position: relative;
  &::before {
    position: absolute;
    left: 0px;
    top: 7px;
    content: "";
    display: block;
    height: 4px;
    width: 4px;
    background-color: #ff0000;
  }
}
.form_wrapper {
  text-align: left;
  display: flex;
  flex-direction: column;
  .form_title {
    line-height: 40px;
    color: #333333;
    font-size: 14px;
    padding-left: 20px;
    height: 40px;
    border-bottom: 1px solid #f5f5f5;
  }
  .form_content {
    flex: 1;
    overflow: auto;
    ul {
      padding-top: 20px;
    }
    li {
      color: #333333;
      padding: 30px 0px 0px 20px;
      h4 {
        font-size: 14px;
        color: #b2b2b2;
        padding-bottom: 10px;
      }
      & > div {
        font-size: 12px;
      }
      .words-code {
        height: 24px;
      }
      .main-word {
        min-width: 208px;
        .main-word-content {
          height: 34px;
        }
      }
      .word-weight {
        margin-left: 32px;
        max-width: 64px;
        .word-weight-content {
          height: 34px;
        }
      }
    }

    .all_words {
      h4 {
        margin-top: 10px;
      }

      .el-tag {
        position: relative;
        font-size: 12px;
        color: #333;
        background-color: #f5f5f5;
        max-width: 108px;
        // min-width:80px;
        border-radius: 16px;
        overflow: hidden;
        text-overflow: ellipsis;
        border: none;
        padding-right: 24px;
      }
      i {
        position: absolute !important;
        top: 6.5px;
        right: 5px;
        color: #333;
        &:hover {
          color: @color;
          background-color: #fff;
        }
      }
      .el-tag {
        margin-right: 10px;
        margin-bottom: 10px;
        height: 30px;
      }
      .add_input {
        display: inline-block;
        vertical-align: top;
        height: 30px;
      }
      .button-new-tag {
        border: none;
        vertical-align: top;
        font-size: 30px;
        line-height: 30px;
        padding-top: 0;
        padding-bottom: 0;
        padding: 0 7px;
        border-radius: 50%;
        background-color: #f5f5f5;
      }
      .input-new-tag {
        border: none;
        display: inline-block;
        vertical-align: top;
        width: 90px;
        height: 30px;
        input {
          display: inline-block;
          height: 30px;
        }
      }
    }
    .change-words {
      display: flex;
    }
  }
}
</style>




