
<template>
  <div id="analyse">
    <div class="header">
      <div class="title">
        <p></p>
      </div>
      <div class="right">
        <div class="search" title="">
          <template>
            <el-select
              v-model="search_content"
              filterable
              remote
              clearable
              placeholder=""
              :remote-method="remoteMethod"
              @change="getLabel"
              :loading="loading">
              <!-- <i slot="suffix" class="el-input__icon el-icon-date"></i> -->
              <el-option
                v-for="(item,index) in search_select_list"
                :key="index"
                :label="item.label"
                :value="item.value"
                :title="item.label">
              </el-option>
            </el-select>
          </template>
        </div>
        <div class="get-timer" title="">
          <div class="choice">
            <el-date-picker
              v-model="getTimes"
              size="mini"
              type="daterange"
              align="right"
              unlink-panels
              :clearable="false"
              range-separator="-"
              start-placeholder=""
              end-placeholder=""
              :picker-options="pickerOptions2"          
              format="yyyy / MM / dd "
              value-format="yyyy-MM-dd">
          </el-date-picker>
          </div>
        </div>
        <div class="dataSelect" title="">
          <template>
            <el-select v-model="typeValue" placeholder="">
              <el-option
                v-for="item in dateOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </div>
        <div class="xlsx" title="" @click="exportExcel">
          <i class="el-icon-download"></i>
        </div>
      </div>
    </div>
    <div class="article">
      <div class="draw-charts">
        <template>
          <el-radio-group v-model="conditions.chart_type">
            <el-radio :label=0></el-radio>
            <el-radio :label=1></el-radio>
            <el-radio :label=2></el-radio>
          </el-radio-group>
        </template>
      </div>
      <div class="charts">
        <charts :getData="conditions"></charts>
      </div>
      <div class="chart-table">
        <three-table :getIdPage="conditions"></three-table>
      </div>
      </div>
  </div>
    
</template>
<script>
import charts from "../blocks/analyse/three_charts";
import threeTable from "../blocks/analyse/three_table";
//
import FileSaver from "file-saver";
import XLSX from "xlsx";
export default {
  name: "analyse",
  data() {
    return {
      typeValue: 1,
      dateOptions: [
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        },
        {
          value: 3,
          label: ""
        }
      ],
      //
      initData: [],
      getTimes: [
        new Date(new Date().getTime() - 3600 * 1000 * 24 * 30).toLocaleString(),
        new Date(new Date().getTime()).toLocaleString()
      ],
      //
      conditions: {
        faq_id: "",
        search_content: "",
        chart_type: 0,
        start_time: "",
        end_time: "",
        page: 1,
        type: 1
      },
      // sendId_page: {
      //   id: this.search_content,
      //   page: 1,
      //   time: this.getTime
      // },
      search_content: "",
      search_select_list: [],
      list: [],
      loading: false,
      //
      pickerOptions2: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ],
        disabledDate(data){
          // console.log(data);
          return new Date(data)>new Date();
        }
      }
    };
  },
  watch: {
    conditions: {
      handler(cur) {
        console.log(cur, 666);
        this.sendData = cur;
      },
      deep: true
    },
    //
    search_content(cur, old) {
      if (cur) {
        console.log(cur);
        let obj = {};
        obj = this.initData.find(item => {
          //userList
          return item.id == cur; //
        });
        console.log(obj); //namelabel
        this.conditions.search_content = obj.question;
        this.conditions.faq_id = obj.id;
      } else {
        this.conditions.search_content = "";
        this.conditions.faq_id = "";
      }
    },
    //
    getTimes: {
      handler(cur, old) {
        console.log(cur);
        if (!cur) {
          this.conditions.start_time = old[0].split(" ")[0].replace(/\//g, "-");
          this.conditions.end_time = old[1].split(" ")[0].replace(/\//g, "-");
        } else {
          this.conditions.start_time = cur[0];
          this.conditions.end_time = cur[1];
        }
      },
      deep: true
    },
    typeValue(cur) {
      this.conditions.type = cur;
    }
  },
  computed: {},
  created() {
    console.log('1',this.conditions);
    // this.conditions.start_time = this.getTimes[0]
    //   .split(" ")[0]
    //   .replace(/\//g, "-");
    // this.conditions.end_time = this.getTimes[1]
    //   .split(" ")[0]
    //   .replace(/\//g, "-");
    this.conditions.start_time=this.$cm.format((new Date().getTime() - 3600 * 1000 * 24 * 30),"-");
    this.conditions.end_time=this.$cm.format(new Date().getTime(),"-");
    console.log('2',this.conditions);
  },
  mounted() {},
  components: {
    charts,
    threeTable
  },
  methods: {
    exportExcel () {
         /* generate workbook object from table */
         var wb = XLSX.utils.table_to_book(document.querySelector('#three-table'))
         /* get binary string as output */
         var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' });
         var that = this;
         try {
             FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), `${that.conditions.search_content?that.conditions.search_content:""}.xlsx`)
         } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
         return wbout
     },
    getLabel(val) {
      // console.log(val,7899);
      // let obj = {};
      // obj = this.search_select_list.find((item)=>{//userList
      //     return item.id == val;//
      // });
      // console.log(obj.label);//namelabel
    },
    remoteMethod(query) {
      // console.log(query, "");
      if (query !== "") {
        this.loading = true;
        //ajax
        setTimeout(() => {
          this.$cm.ajax(
            "POST",
            "Faq.faq_getaskquestion",
            { keyword: query },
            res => {
              // console.log(res);
              var temp = res.data.result;
              this.initData = temp;
              this.search_select_list = [];
              temp.forEach((element, index) => {
                this.search_select_list.push({
                  label: `${element.class_a}${element.question}`,
                  value: element.id
                });
              });
              this.loading = false;
            },
            this
          );
        }, 200);
      } else {
        this.search_select_list = [];
      }
      // this.conditions.search = this.search_content;
      // console.log(this.search_content, 7888);
    }
  }
};
</script>
<style lang="less">
#app #analyse .el-input--suffix .el-input__inner {
  height: 100%;
}
body .el-select-dropdown__item {
  max-width: 510px;
}
#analyse {
  /* select */
  background-color: #fff;
  padding: 12px 20px;
  .el-radio-group .el-radio__input {
    float: inherit;
  }
  .el-radio + .el-radio {
    display: inline-block;
  }
  .el-radio-group .el-radio__label {
    float: inherit;
  }
  .el-radio__inner::after {
    height: 6px;
    width: 6px;
  }
  display: flex;
  flex-direction: column;
  .header {
    .title {
      float: left;
    }
    .right {
      float: right;
      margin-right: 10px;
      .dataSelect {
        width: 60px;
        .el-select .el-input input {
          font-size: 12px;
          cursor: default;
        }
      }
      .xlsx {
        width: 28px;
        text-align: center;
        line-height: 28px;
        border: 1px solid #dcdfe6;
        border-radius: 50%;
        background-color: #fff;
        cursor: pointer;
        &:hover {
          border-color:#cccccc;
        }
        i {
          font-size: 23px;
          color: #666666;
        }
      }
      & > div {
        margin-right:7px;
        .el-select {
          height: 28px;
        }
        height: 28px;
        display: inline-block;
        vertical-align: middle;
      }
    }
  }
  .article {
    flex: 1;
    overflow: auto;
    .draw-charts {
      padding: 20px 0;
      .el-radio {
        padding-right: 16px;
        .el-radio__label {
          padding-left: 6px;
        }
      }
    }
    .charts {
      height: 400px;
      width: 100%;
    }
    .chart-table {
      display: flex;
      flex-direction: column;
      width: 100%;
    }
  }
}
</style>



