<template>
    <div class="history">
      <div class="table_list">
        <table>
          <thead>
            <th></th>
            <th></th>
            <th>FAQ</th>
            <th></th>
            <th></th>
          </thead>
          <tbody>
            <template v-for="(n,i) in all_lists"> 
              <tr :key="i" >
                <td>{{n.update_time}}</td>
                <td>{{n.uid}}</td>
                <td>{{n.faq_id}}</td>
                <template v-if="n.type==1">
                  <td></td>
                </template>
                <template v-if="n.type==2">
                  <td></td>
                </template>
                <template v-if="n.type==3">
                  <td></td>
                </template>
                <td>
                  <p class="watch_log" @click.stop="look_detail(n.id)"></p>
                </td>
              </tr>             
            </template>            
          </tbody>
        </table>
      </div>
      <div class="pagination_bottom">
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageNum"
          layout="total, prev, pager, next, jumper"
          :total="all_lists_length">
        </el-pagination>
      </div>
      <transition name="el-fade-in-linear">   
      <div class="fade_form" v-if="show_fade" @click.stop="show_fade = !show_fade">
        <history_form :getData="send_data"></history_form>
      </div>
      </transition>
    </div>
</template>
<script>
import history_form from "../blocks/history/history_form";
export default {
  name: "history",
  components: {
    history_form
  },
  data() {
    return {
      articles: [],
      all_lists: [],
      value7: "",
      currentPage: 1,
      show_fade: false,
      all_lists_length: 0,
      send_data: {},
      pageNum: 11
    };
  },
  mounted() {
    this.$cm.ajax(
      "POST",
      "Faq.faq_usermanage",
      { limit: 11 },
      res => {
        this.all_lists = res.data.result.data;
        this.all_lists_length = res.data.result.num - 0;
      },
      this
    );
  },
  computed: {
    username() {
      //  `params` 
      return this.$route.params.username;
    }
  },
  methods: {
    look_detail(id) {
      this.show_fade = true;
      this.send_data = { id: id };
    },
    goBack() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
    },
    dele_card() {
      this.$alert("hello");
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.$cm.ajax(
        "POST",
        "Faq.faq_usermanage",
        { page: val, limit: 11 },
        res => {
          console.log(res);
          this.all_lists = res.data.result.data;
          this.all_lists_length = res.data.result.num - 0;
        },
        this
      );
    }
  }
};
</script>
<style lang="less" scoped>
@color: #24C79E;
.text_es() {
  overflow: hidden;
  text-overflow: ellipsis;
  /* */
  display: -webkit-box;
  /* */
  -webkit-line-clamp: 1;
  /*  */
  -webkit-box-orient: vertical;
}

/*  */
// .form-fade-enter-to,.form-fade-leave{
//   // transform: translateX(0%);
//   opacity: 0;
//   transition: all 1s;
// }

// .form-fade-enter,.form-fade-leave-to{
//   // transform: translateX(100%);
//   // width:0px;
//   opacity: 1;
//   transition: all 1s;
// }

.border() {
  border: 1px solid #cccccc;
}
h3 {
  font-size: 14px;
  font-weight: bold;
}

//
.pagination_bottom {
  text-align: right;
  padding-right: 20px;
  padding-top: 15px;
}

//
.watch_log {
  font-size: 12px;
  color: @color;
  cursor: pointer;
}

.history {
  height: 100%;
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  font-size: 12px;
}

.table_list {
  flex: 1;
  table {
    width: 100%;
    // height: 100%;
    border: 1px solid #cccccc;
    border-collapse: collapse;
    background-color: #fff;
  }
  th,
  tr {
    height: 40px;
  }
  th {
    font-size: 14px;
    text-align: left;
    color: #151515;
    border-bottom: 1px solid #cccccc;
    &:nth-child(1) {
      padding-left: 68px;
    }
  }
  tr {
    padding-left: 68px;
    &:hover {
      background-color: #e4f9ff;
    }
    td {
      min-width: 160px;
      &:nth-child(1) {
        padding-left: 68px;
      }
      &:nth-child(2) {
        min-width: 100px;
      }
      &:nth-child(3) {
        min-width: 552px;
      }
    }
  }
}
</style>




