      height: 180px;
      width: 100%;
      background-color: #ffffff;
      overflow-x: auto;
      overflow-y: hidden;
      padding:0;
    }
    .all_words {
      background-color: #f8f8f8;
    }
    .simple_words textarea {
			border: none;
			font-size: 13px;
			font-family: Helvetica; 
    }
