<template>
  <div class="form_wrapper" @click.stop="keep_modal">
            <div class="form_title">
              FAQ
            </div>
            <div class="form_content">
              <!--  -->
              <div class="inputBox">
                <div class="inputValue yo-el-input">
                  <div class="searchBox">
                    <input
                        type="text"
                        placeholder=""
                        v-model="similarAsk" v-on:input="getsimilarfaq(similarAsk)"/>   
                        <span></span>
                    </div>
                </div>                  
              </div>
              <!-- faq -->
               <div class="alike_box" v-if="search_show"> 
                  <h4>FAQ</h4>
                  <el-radio-group v-model="radio_faq" text-color="#4fb4ff" fill="#4fb4ff">
                    <template v-for="(item,index) in similarFaq">
                    <el-radio  class="alike_radio" :label="item.id" :key="item.id" :title="item.question"><span :key="index" style="float:left;padding-right:6px;" >[{{item.class}}]</span>{{item.question}}</el-radio>                    
                    </template>
                  </el-radio-group>                  
                </div> 
              <!--  -->
              <div class="recommend yo-el-checkbox">
                <h4>({{checkList.length}})</h4>
                <el-checkbox-group v-model="checkList" >
                  <template v-for="(item,index) in getsimilarask">
                    <el-checkbox :label="item.question" :key="index" :id="item.id" @change="end_value(item.id)" :checked="index===0?true:false" :title="item.question"></el-checkbox>
                  </template> 
                </el-checkbox-group>
              </div>
            </div>
            <div class="form_btn">
                <div class="yo-btn">
                    <el-button round @click.stop="close_modal"></el-button>
                    <el-button type="primary" round  @click.stop="saveEditFaq()"></el-button>
                </div>
            </div>
          </div>
</template>
<script>
import edit_form from "../edit_form.vue";
export default {
  name: "history_form",
  data() {
    return {
      /*  */
      radio_faq: "",
      checkList: [],
      slectedSimilarAsk: [{id:this.getData.id,question:this.getData.title}],
      getsimilarask: [],
      getsimilarask: this.getData.all_similar,
      confirm_modal: false,
      search_show: true,
      add_show: false,
      form_content: [
        // {
        //   title: "FAQ",
        //   content: "[]",
        //   word: ""
        // },
        // {
        //   title: "",
        //   content: "",
        //   word: ""
        // },
        // {
        //   title: "",
        //   content: "",
        //   word: ""
        // },
        // {
        //   title: "",
        //   content: "",
        //   word: ""
        // }
      ],
      similarAsk: "",
      similarFaq: [],
      slectedId: this.getData.id
    };
  },
  props: {
    getData: {
      type: Object
    }
    // 'showModal'
  },
  mounted() {
    this.init_checkbox();
  },
  components: {
    edit_form
  },
  methods: {
    init_checkbox() {
      this.getsimilarfaq(this.getData.title);
      this.getsimilarask = this.getData.all_similar;
    },
    saveEditFaq() {
      var id, arr, uid;
      id = this.getData.id;
      arr = this.slectedSimilarAsk;
      uid = this.$cm.uid;
      console.log(this.$cm)
      var params = {
        id: this.radio_faq,
        similar_data: arr,
        uid: uid
      };
      this.$cm.ajax("POST","Faq.faq_pointsimilarfaq",params,(res)=>{
        this.close_modal();
        this.$message("");
        console.log(res)
        },this)
      console.log(params);
    },
    getsimilarfaq(similarAsk) {
      this.search_show = true;
      this.$cm.ajax(
        "POST",
        "Faq.faq_getsimilarfaq",
        { ask:similarAsk },
        res => {
          this.similarFaq = res.data.result.data;
        },
        this
      );
      /*  */
      // this.$cm.ajax(
      //   "POST",
      //   "Faq.faq_getsimilarask",
      //   { ask:similarAsk },
      //   res => {
      //     this.getsimilarask = res.data.result.data;
      //   },
      //   this
      // );
    },
    get_send_arr() {},
    close_modal() {
      this.$parent.show_fade = false;
    },
    keep_modal() {
      this.$parent.show_fade = true;
    },
    setValue() {},
    end_value(id) {
      console.log(this.getsimilarask);
      this.slectedSimilarAsk = [];
      for (var i = 0; i < this.checkList.length; i++) {
        this.slectedSimilarAsk.push({
          question: this.checkList[i]
        });
      }
      for (var i = 0; i < this.checkList.length; i++) {
        for (var j = 0; j < this.getsimilarask.length; j++) {
          if (this.checkList[i] == this.getsimilarask[j].question) {
            this.slectedSimilarAsk[i].id = this.getsimilarask[j].id;
          }
        }
      }
      console.log(this.slectedSimilarAsk);
    },
    state_value() {},
    checked_value() {}
  }
};
</script>
<style lang="less" scoped>
@line: 1px solid #edf0f5;
@color: #4fb4ff;
.text_es() {
  overflow: hidden;
  text-overflow: ellipsis;
  /* */
  display: -webkit-box;
  /* */
  -webkit-line-clamp: 1;
  /*  */
  -webkit-box-orient: vertical;
}
.require {
  padding-left: 14px;
  position: relative;
  &::before {
    position: absolute;
    left: 0px;
    top: 7px;
    content: "";
    display: block;
    height: 4px;
    width: 4px;
    background-color: #ff0000;
  }
}
.addOther {
  display: inline-block;
  color: #409eff;
  padding: 12px 0px;
  cursor: pointer;
}
.form_wrapper {
  text-align: left;
  display: flex;
  flex-direction: column;
  /*  */
  .form_title {
    line-height: 40px;
    color: #333333;
    font-size: 14px;
    padding-left: 20px;
    height: 40px;
    border-bottom: 1px solid #f5f5f5;
  }
  /*  */
  .form_content {
    flex: 1;
    padding: 24px;
    overflow: auto;
    .selectBox {
      margin-bottom: 20px;
      .el-select {
        padding: 10px 5px;
        border-radius: 53px;
        overflow: hidden;
        font-size: 14px;
        color: #333333;
      }
      .selectValue {
        display: flex;
        width: 100%;
        height: 100%;
      }
      .selectLabel {
        width: 100%;
      }
    }
    .inputValue {
      margin-top: 10px;
      margin-bottom: 20px;
      height: 36px;
      .searchBox {
        float: right;
        height: 100%;
        width: 100%;
        border-radius: 18px;
        padding-left: 12px;
        overflow: hidden;
        display: flex;
        border: @line;
        input {
          flex: 1;
          height: 100%;
          border: none;
          outline: none;
        }
        span {
          height: 100%;
          width: 28px;
          background: url(/src/assets/guanli_search.png) no-repeat 0px center;
          background-size: 55%;
          padding: 2px;
          cursor: pointer;
        }
      }
      input {
        height: 100%;
      }
    }
    .alike_box {
      // margin-bottom: 20px;
      position: relative;
      h4 {
        color: #b2b2b2;
        // padding-top: 26px;
        padding-bottom: 20px;
      }
      .alike_q {
        padding: 10px 0px 0px 0px;
        color: #409eff;
        max-width: 70%;
        .text_es();
        cursor: pointer;
      }
      .alike_radio {
        height: 33px;
        line-height: 33px;
        color: #b2b2b2;
        max-width: 100%;
        cursor: pointer;
      }
      .modal_q {
        height: 68px;
        width: 180px;
        border: 1px solid #dcdfe6;
        border-radius: 0px 12px 0px 0px;
        position: absolute;
        right: 0px;
        top: 0px;
        text-align: center;
        padding: 10px;
        background-color: #fefefe;
        box-shadow: 3px 4px #cccccc;
        h4 {
          padding-bottom: 8px;
        }
        button {
          height: 22px;
          width: 48px;
          font-size: 12px;
          display: inline-block;
          // line-height: 1;
          white-space: nowrap;
          cursor: pointer;
          background: #fff;
          border: 1px solid #dcdfe6;
          color: #606266;
          -webkit-appearance: none;
          text-align: center;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          outline: 0;
          margin: 0;
          -webkit-transition: 0.1s;
          transition: 0.1s;
          font-weight: 500;
          line-height: 22px;
          border-radius: 14px;
          margin: 0px 8px;
          &:hover {
            color: #409eff;
            border-color: #c6e2ff;
            background-color: #ecf5ff;
          }
          &:nth-last-child(1) {
            background-color: #409eff;
            color: #ffffff;
            &:hover {
              background: #66b1ff;
              border-color: #66b1ff;
              color: #fff;
            }
          }
        }
      }
    }
    .ql_content {
      margin-top: 10px;
      // margin-bottom:20px;
    }
    .recommend {
      h4 {
        padding-top: 26px;
        padding-bottom: 20px;
      }
      .el-checkbox {
        padding-bottom: 14px;
      }
    }
  }
  /*  */
  .form_btn {
    text-align: right;
    padding: 24px;
    .yo-btn {
      .el-button {
        padding: 11px 25px;
      }
      .el-button + .el-button {
        margin-left: 20px;
      }
    }
  }
}
</style>




