<template>
  <div class="info_content">
    <ul>
      <li class="title">
        <div class="temp_line"></div>
        <div class="title_content"></div>
        <div class="temp_line"></div>
      </li>
      <li class="image_name">
        <img src="../../assets/logo.png" alt="" srcset="">
        <p>admin</p>
      </li>
      <li class="detail_info">
        <p>:<span>18810242990</span></p>
        <p>:<span>hyfscript@163.com</span></p>
      </li>
    </ul>
  </div>
</template>
<script>
export default {};
</script>

<style lang="less" scoped>
.info_content {
  @line: 1px solid #dce2e6;
  color: #333333;
  background-color: #ffffff;
  text-align: center;
  position: relative;
  & > ul {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .title {
    // height:50px;
    // margin-top: 72px;
    & > div {
      display: inline-block;
      vertical-align: middle;
    }
    .temp_line {
      width: 70px;
      height: 1px;
      // line-height: 50px;
      border-bottom: @line;
    }
    .title_content {
      font-size: 24px;
      padding: 0px 24px;
    }
  }
  .image_name {
    margin-top: 46px;
    img {
      width: 148px;
      height: 148px;
      object-fit: cover;
      border: @line;
      border-radius: 50%;
    }
    p {
      margin-top: 30px;
      margin-bottom: 34px;
      font-size: 20px;
      color: #333333;
    }
  }
  .detail_info {
    width: 900px;
    border-top: @line;
    font-size: 14px;
    margin: 0 auto;
    padding-top: 44px;
    p {
      line-height: 29px;
    }
  }
}
</style>





